import instance from './instance'
const axios = instance('/b/cs')

export default class BillingSOAServices {
  static getMembershipFees ({ journalId }) {
    return axios({
      url: `/billing/client-soa/${journalId}/compute/membership-fees/`,
      method: 'GET'
    })
  }

  static getProcessingFees ({ journalId, memberId }) {
    return axios({
      url: `/billing/client-soa/${journalId}/compute/processing-fees/`
    })
  }
}
