const supervisorBreadcrumbs = {
  dashboard: {
    text: 'Dashboard',
    to: '/'
  },
  clients: {
    text: 'Clients',
    to: '/clients'
  },
  ledger: {
    text: 'Ledger',
    to: '/ledger'
  },
  ledgers: {
    text: 'LIST OF CLIENT SOAs TO VALIDATE',
    to: data => `/billing/ledgers`
  },
  ledgerId: {
    text: data => data,
    to: data => `/billing/ledgers/${data}`
  },
  transactionLedger: {
    text: 'TRANSACTION LEDGERS',
    to: '/transaction'
  },
  assign: {
    text: 'Assign',
    to: '/assign'
  },
  createTransaction: {
    text: 'CREATE TRANSACTION',
    to: '/billing/create/transaction'
  },
  journal: {
    text: 'TRANSACTION JOURNAL',
    to: '/billing/journal/'
  },
  accountList: {
    text: 'LIST OF ACCOUNTS',
    to: '/billing/journal/accounts'
  },
  reports: {
    text: 'REPORTS',
    to: '/reports'
  },
  assignId: {
    text: data => data,
    to: data => `/assign/${data}`
  },

  clientJournal: {
    text: data => `${data}/TRANSACTION JOURNAL`,
    to: data => `/billing/transaction/ledgers/${data}/TRANSACTION JOURNAL`
  }
}

function BillingAnalystRoutes () {
  const [userRole, setUserRole] = React.useState('')
  const [isLoading, setIsLoading] = React.useState(true)

  React.useEffect(() => {
    document.title = 'HIMS - Billing'
    const { role } = JSON.parse(localStorage.getItem('me'))
    setUserRole(role)
    setIsLoading(false)
  })

  SideBarService.setItems(sideBarItems)

  if (userRole === 'BILLING_ANALYST') {
    BreadcrumbsService.setItems(analystBreadcrumbs)
  } else {
    BreadcrumbsService.setItems(supervisorBreadcrumbs)
  }

  console.log(ability)

  return (
    <Switch>
      {userRole === 'BILLING_ANALYST' && <Route path='/' exact component={DashboardAnalyst} />}
      {/* {userRole === 'BILLING_ANALYST' && <Route path='/Supervisor' exact component={DashboardSupervisor} />} */}
      {userRole === 'BILLING_SUPERVISOR' || userRole === 'BILLING_MANAGER' ? (
        <Route path='/' exact component={DashboardSupervisor} />
      ) : null}
      {userRole === 'BILLING_SUPERVISOR' || userRole === 'BILLING_MANAGER' ? (
        <Route path='/Analyst' exact component={DashboardAnalyst} />
      ) : null}
      {/* {userRole === 'BILLING_SUPERVISOR' || userRole === 'BILLING_MANAGER' ? (
        <Route path='/Supervisor' exact component={DashboardSupervisor} />
      ) : null} */}

      <Route path='/clients/ledger/' exact component={ClientTransactionSetupPage} />
      <Route path='/clients/analyze/:_id/:account_number' exact component={BillingCycleSetupPage} />
      <Route path='/billing/invoice/form/:transactionNumber/:transactionStatus' exact component={InvoicePendingPage} />
      <Route path='/billing/ledgers/:soaNumber/:type' exact component={SOAPage} />
      <Route path='/billing/ledgers-validated/:soaNumber/:type' exact component={ValidatedSOAPage} />
      <Route path='/billing/journal/accounts' exact component={ListOfAccountsPage} />
      <Route path='/billing/journal/transactions/:clientId' exact component={ClientTransactionJournalPage} />
      <Route path='/billing/invoice' exact component={InvoiceApprovedPage} />
      <Route path='/billing/invoice/approved/:transaction_number' exact component={InvoiceApprovedPage} />
      <Route path='/billing/create/transaction' exact component={CreateTransactionPage} />
      <Route path='/billing/create/transaction/type' exact component={CreateTransactionTypePage} />
      <Route path='/billing/create/transaction/credit' exact component={CreateTransactionCreditNotePage} />
      <Route path='/billing/create/transaction/debit' exact component={CreateTransactionDebiNotetPage} />

      <Route path='/clients/assign/:_id/:account_number' exact component={AssignAnalystPage} />
    </Switch>
  )
}

export default BillingAnalystRoutes
