import React from 'react'
import { useHistory } from 'react-router-dom'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'

// Styles
import classes from './ConfirmDialog.module.scss'
import { Typography, DialogContent, Button } from '@material-ui/core'

// Services
import BillingSupervisorServices from 'Services/Api/BillingSupervisorServices'

const SuccessDialog = props => {
  const { openSuccessDialog, setOpenSuccessDialog } = props
  const history = useHistory()

  const handleOnCloseSuccessDialog = () => {
    setOpenSuccessDialog(false)
    history.push('/')
  }

  return (
    <Dialog
      fullWidth
      maxWidth='xs'
      onClose={handleOnCloseSuccessDialog}
      aria-labelledby='customized-dialog-title'
      open={openSuccessDialog}
      className={classes.root}
      PaperProps={{ style: { height: '40%', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <IconButton
            aria-label='close'
            id='success-close-button'
            className={classes['success-close-button']}
            onClick={handleOnCloseSuccessDialog}
          >
            <CloseIcon />
          </IconButton>
          <Typography variant='h6' id='assign-title' className={classes.title}>
            Account Successfully Assigned
          </Typography>
        </div>
      </DialogTitle>
      <DialogContent>
        Account was successfully assigned to Billing Analyst and sent for billing cycle setup.
        <div className={classes.button}>
          <Button
            variant='contained'
            id='ok-button'
            className={classes['button-positive']}
            onClick={handleOnCloseSuccessDialog}
          >
            OK
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  )
}

export default function CustomizedDialogs (props) {
  const { assign, setAssign, onCancel } = props
  const [openSuccessDialog, setOpenSuccessDialog] = React.useState(false)

  const handleOnClose = () => {
    setAssign(state => ({ ...state, show: false }))
  }

  const onShowSuccessDialog = () => {
    setAssign(state => ({ ...state, show: false }))
    BillingSupervisorServices.assignBillingAnalyst({
      analystId: assign.analyst,
      data: { clientId: assign.clientId }
    }).then(res => {
      setOpenSuccessDialog(true)
    })
  }

  return (
    <>
      <Dialog
        maxWidth='xs'
        open={assign.show}
        className={classes.root}
        PaperProps={{ style: { display: 'flex' } }}
        onClose={handleOnClose}
      >
        <DialogTitle disableTypography>
          <Typography>{assign.title}</Typography>
        </DialogTitle>
        <DialogContent>
          <Typography>{assign.text}</Typography>
          <br />
          <Typography>{assign.text2}</Typography>

          <div className={classes.button}>
            <Button variant='outlined' className={classes['button-negative']} onClick={onCancel}>
              Cancel
            </Button>
            <Button variant='contained' className={classes['button-positive']} onClick={onShowSuccessDialog}>
              Assign
            </Button>
          </div>
        </DialogContent>
      </Dialog>

      <SuccessDialog openSuccessDialog={openSuccessDialog} setOpenSuccessDialog={setOpenSuccessDialog} />
    </>
  )
}
