import React from 'react'
import clsx from 'clsx'

import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'

import { withStyles } from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
// import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import useAutocomplete from '@material-ui/lab/useAutocomplete'
import { makeStyles } from '@material-ui/core/styles'
import { Link } from 'react-router-dom'
import IconButton from '@material-ui/core/IconButton'
import InputAdornment from '@material-ui/core/InputAdornment'
import FormControl from '@material-ui/core/FormControl'
import SearchIcon from '@material-ui/icons/Search'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircle, faAngleDown } from '@fortawesome/free-solid-svg-icons'

import classes from './EnhancedSearchComponent.module.scss'

const useStyles = makeStyles(theme => ({
  label: {
    display: 'block'
  },
  input: {
    width: 100
  },
  listbox: {
    marginTop: '40px',
    width: '190px',
    margin: 0,
    marginLeft: '-10px',
    padding: 0,
    zIndex: 1,
    position: 'absolute',
    listStyle: 'none',
    backgroundColor: theme.palette.background.paper,
    overflow: 'auto',
    maxHeight: 400,
    '& li:active': {
      // backgroundColor: '#2977f5',
      color: 'white'
    }
  }
}))

const StyledMenu = withStyles({
  paper: {
    marginTop: '5px'
    // borderTop: '0.5px solid rgba(0, 0, 0, 0.54)'
  }
})(props => (
  <Menu
    elevation={0}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'left'
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'left'
    }}
    {...props}
  />
))

const StyledMenuItem = withStyles(theme => ({
  root: {
    textAlign: 'center',
    padding: '15px 20px 15px 20px',
    '& .MuiListItemIcon-root, & .MuiListItemText-primary': {
      top: '289px',
      color: 'rgba(0, 0, 0, 0.54)',
      fontSize: '15px'
    }
  }
}))(MenuItem)

const clientInfo = [
  { client_info_no: '95-987-6530', name: 'Happy Innovative, Inc', date: 'Feb 05, 2019 to Feb 05, 2020' },
  { client_info_no: '94-987-6599', name: 'Diane Lee Fashion', date: 'Jan 01, 2019 to Dec 31, 2019' }
]

export default function EnhancedSearchComponent (props) {
  const { id, onChange, placeholder = '', width, children, menuItems } = props

  const [anchorEl, setAnchorEl] = React.useState(null)
  const [currentSelected, setCurrentSelected] = React.useState('Member')

  const handleClick = event => {
    setAnchorEl(event.currentTarget)
  }

  const handleClose = () => {
    setAnchorEl(null)
  }

  const {
    getRootProps,
    getInputLabelProps,
    getInputProps,
    getListboxProps,
    getOptionProps,
    groupedOptions
  } = useAutocomplete({
    id: 'search-input',
    options: clientInfo,
    getOptionLabel: option => option.client_info_no
  })

  const autoCompClasses = useStyles()

  return (
    <div>
      <Paper id='enhanced-search-component-wrapper' style={{ width: width }} className={classes.root}>
        <div>
          <Button
            id='drop-down-button'
            className={classes['menu-button']}
            aria-controls='customized-menu'
            aria-haspopup='true'
            onClick={handleClick}
            style={{ marginRight: '10px', borderRadius: '0px', width: '100px', textAlign: 'left' }}
            endIcon={<FontAwesomeIcon style={{ fontSize: '20px', marginBottom: '-3px' }} icon={faAngleDown} />}
          >
            {currentSelected}
          </Button>
          <StyledMenu
            id='drop-down-items'
            anchorEl={anchorEl}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={handleClose}
          >
            <StyledMenuItem
              id='drop-down-item-1'
              onClick={e => {
                setCurrentSelected('Member')
                setAnchorEl(null)
              }}
            >
              <ListItemText id='drop-down-item-1-label'>Member</ListItemText>
            </StyledMenuItem>
            <StyledMenuItem
              id='drop-down-item-2'
              onClick={e => {
                setCurrentSelected('Client')
                setAnchorEl(null)
              }}
            >
              <ListItemText id='drop-down-item-2-label'>Client</ListItemText>
            </StyledMenuItem>
          </StyledMenu>
        </div>
        <FormControl
          id='search-input-wrapper'
          className={clsx(classes.margin, classes.withoutLabel, classes.textField)}
        >
          <InputBase
            id='search-input'
            // endAdornment={
            //   <InputAdornment position='end'>
            //     <IconButton
            //       style={{ display: 'inline-block', padding: 0, minHeight: 0, minWidth: 0 }}
            //       aria-label='toggle password visibility'
            //     >
            //       <SearchIcon />
            //       <span style={{ fontSize: '14px' }}>Search</span>
            //     </IconButton>
            //   </InputAdornment>
            // }
            aria-describedby='standard-weight-helper-text'
            inputProps={{
              'aria-label': 'weight'
            }}
            className={autoCompClasses.input}
            {...getInputProps()}
          />
          {groupedOptions.length > 0 ? (
            <ul className={autoCompClasses.listbox} {...getListboxProps()}>
              {groupedOptions.map((option, index) => (
                <li id={`autocomplete-item-${index}`} {...getOptionProps({ option, index })}>
                  <div
                    style={{
                      height: '80px',
                      borderBottom: '0.5px solid rgba(0, 0, 0, 0.54)',
                      padding: '15px',
                      fontSize: '12px'
                    }}
                  >
                    <div>
                      <FontAwesomeIcon
                        style={{ float: 'left', color: ' #0d5e40', margin: '0px 18px -10px 0px' }}
                        icon={faCircle}
                      />
                    </div>
                    {option.client_info_no}
                    <div>{option.name}</div>
                    <div>{option.date}</div>
                  </div>
                </li>
              ))}
            </ul>
          ) : null}
        </FormControl>
        <IconButton
          id='search-button'
          style={{ display: 'inline-block', padding: 0, minHeight: 0, minWidth: 0 }}
          aria-label='toggle password visibility'
        >
          <SearchIcon id='search-icon' />
          <span id='search-label' style={{ fontSize: '14px' }}>
            Search
          </span>
        </IconButton>
      </Paper>
    </div>
  )
}
