import React from 'react'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'

// components
import Void from 'StandardComponents/Void/Void'

// styles
import classes from './ContentPageComponent.module.scss'

export default function ContentPage (props) {
  const {
    voidFunction,
    type,
    shift,
    soaPage,
    getStepContent,
    activeStep,
    save,
    steps,
    nextStep,
    stepBack,
    reset,
    content
  } = props
  const [openDialog, setOpenDialog] = React.useState(false)

  const handleVoid = () => {
    setOpenDialog(true)
  }

  return (
    <div className={`${classes.root} ${shift ? classes.contentShift : ''}`}>
      {soaPage ? (
        <div>
          {type === 'analyst' ? (
            <div>
              {activeStep === 4 ? (
                <Typography className={classes.instructions}>{getStepContent({ stepIndex: 3, activeStep })}</Typography>
              ) : (
                <Typography className={classes.instructions}>{getStepContent({ stepIndex: activeStep })}</Typography>
              )}
              <div className={classes.button}>
                <Button
                  variant='contained'
                  id={`${activeStep}-prev-button`}
                  onClick={stepBack}
                  className={classes['button-negative']}
                >
                  {activeStep === 0 ? 'Cancel' : 'Back'}
                </Button>
                <div className={classes['item-right-side']}>
                  <Button variant='contained' id={'save-button'} onClick={save} className={classes['button-positive']}>
                    Save
                  </Button>
                  &emsp;&emsp;
                  <Button
                    variant='contained'
                    id={`${activeStep}-next-button`}
                    className={classes['button-positive']}
                    onClick={nextStep}
                  >
                    {activeStep === 3 || activeStep === 4 ? 'Submit' : 'Next'}
                  </Button>
                </div>
              </div>
            </div>
          ) : (
            ''
          )}

          {type === 'supervisor' ? (
            <div>
              {activeStep === 4 ? (
                <Typography className={classes.instructions}>{getStepContent({ stepIndex: 3, activeStep })}</Typography>
              ) : (
                <Typography className={classes.instructions}>{getStepContent({ stepIndex: activeStep })}</Typography>
              )}
              <div className={classes.button}>
                <Button
                  variant='contained'
                  id={`${activeStep}-prev-button`}
                  onClick={stepBack}
                  className={classes['button-negative']}
                >
                  {activeStep === 0 ? 'Cancel' : 'Back'}
                </Button>
                <div className={classes['item-right-side']}>
                  <Button
                    variant='contained'
                    id={'void-button'}
                    onClick={handleVoid}
                    className={classes['button-void']}
                  >
                    Void
                  </Button>
                  &emsp;&emsp;
                  <Button
                    variant='contained'
                    id={`${activeStep}-next-button`}
                    className={classes['button-positive']}
                    onClick={nextStep}
                  >
                    {activeStep === 3 || activeStep === 4 ? 'Approve' : 'Next'}
                  </Button>
                </div>
              </div>
              {openDialog === true ? (
                <Void openDialog={openDialog} setOpenDialog={setOpenDialog} submitFunction={voidFunction} />
              ) : (
                ''
              )}
            </div>
          ) : (
            ''
          )}
        </div>
      ) : content != null ? (
        content
      ) : (
        'Test'
      )}
    </div>
  )
}
