import React from 'react'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

// Components
import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'
import { CustomTab, CustomTabs } from 'StandardComponents/CustomTabs/CustomTabs'

import BillingAnalystTab from '../../Components/BillingAnalystTab/BillingAnalystTab'
// import BillingCycleTab from '../Components/BillingCycleTab/BillingCycleTab'
import BillingInstructionsTab from '../../Components/BillingInstructionsTab/BillingInstructionsTab'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import BillingSupervisorServices from 'Services/Api/BillingSupervisorServices'

// CSS
import classes from './AssignClientPage.module.scss'

const header_items = [
  {
    id: 'assign',
    numeric: false,
    center: false,
    disablePadding: false,
    label: 'Assign Billing Analyst',
    width: '50px'
  },
  {
    id: 'name',
    numeric: false,
    center: false,
    disablePadding: false,
    label: 'Name'
  },
  {
    id: 'status',
    numeric: false,
    center: true,
    disablePadding: false,
    label: 'Status',
    width: '70px'
  },
  {
    id: 'assigned_count',
    numeric: false,
    center: true,
    disablePadding: false,
    label: 'Number of Accounts'
  },
  {
    id: 'assigned',
    numeric: false,
    center: false,
    disablePadding: false,
    label: 'Accounts Assigned'
  },
  {
    id: 'action',
    numeric: true,
    center: false,
    disablePadding: false,
    label: ''
  }
]

const ClientInformation = ({ classes, clientInfo, hmoinfo }) => {
  return (
    <>
      {clientInfo && (
        <Paper className={classes.client_details}>
          <div className={classes['client-details-wrapper']}>
            <div className={classes['corporate-name-wrapper']}>
              <Typography className={classes['corporate-name-value']}>{clientInfo.client.account_name}</Typography>
            </div>
            <div className={classes['basic-hmo-information-wrapper']}>
              <div className={classes['left-container']}>
                <Typography className={classes['information-title']}>Basic Information</Typography>
                <div className={classes['basic-information-wrapper']}>
                  <div className={classes.field}>
                    <Typography className={classes.text} color='textSecondary'>
                      ACCOUNT NO.
                    </Typography>
                    <Typography className={classes.text} color='textSecondary'>
                      CLIENT SEGMENT
                    </Typography>
                    <Typography className={classes.text} color='textSecondary'>
                      PAYMENT TERM
                    </Typography>
                    <Typography className={classes.text} color='textSecondary'>
                      ANNUAL BENEFIT LIMIT
                    </Typography>
                  </div>
                  <div className={classes.value}>
                    {clientInfo.client.account_number === '' ? (
                      <Typography className={classes.text}>-</Typography>
                    ) : (
                      <Typography className={classes.text}>{clientInfo.client.account_number}</Typography>
                    )}
                    {clientInfo.client.segment === '' || !clientInfo.client.segment ? (
                      <Typography className={classes.text}>-</Typography>
                    ) : (
                      <Typography className={classes.text}>{clientInfo.client.segment}</Typography>
                    )}
                    {clientInfo.contract.payment_terms === '' ? (
                      <Typography className={classes.text}></Typography>
                    ) : (
                      <Typography className={classes.text}>{clientInfo.contract.payment_terms}</Typography>
                    )}
                    {/* Did not remove. To be used as future reference. */}
                    {/* {clientInfo.contract.annual_benefit_limit === 0 ? (
                      <Typography className={classes.text}>{'Php ' + CurrencyFormatter(0)}</Typography>
                    ) : (
                      <Typography className={classes.text}>
                        {'Php ' + CurrencyFormatter(clientInfo.contract.annual_benefit_limit)}
                      </Typography>
                    )} */}
                    <Typography className={classes.text}>{clientInfo.contract.payment_term}</Typography>
                  </div>
                </div>
              </div>
              <div className={classes['center-container']}>
                <div className={classes['basic-information-wrapper']}>
                  <div className={classes.field}>
                    <Typography className={classes.text} color='textSecondary'>
                      TAX TYPE
                    </Typography>
                    <Typography className={classes.text} color='textSecondary'>
                      TIN NO.
                    </Typography>
                  </div>
                  <div className={classes.value}>
                    {clientInfo.client.tax_type === '' ? (
                      <Typography className={classes.text}>-</Typography>
                    ) : (
                      <Typography className={classes.text}>{clientInfo.client.tax_type}</Typography>
                    )}
                    {clientInfo.client.tin === '' ? (
                      <Typography className={classes.text}>-</Typography>
                    ) : (
                      <Typography className={classes.text}>{clientInfo.client.tin}</Typography>
                    )}
                  </div>
                </div>
              </div>
              <div className={classes['right-container']}>
                <Typography className={classes['information-title']}>HMO information</Typography>
                <div className={classes['hmo-information-wrapper']}>
                  <div className={classes.field}>
                    <Typography>
                      <Typography className={classes.text} color='textSecondary'>
                        ACCOUNT STATUS
                      </Typography>
                      <Typography className={classes.text} color='textSecondary'>
                        CONTRACT
                      </Typography>
                      <Typography className={classes.text} color='textSecondary'>
                        CONTRACT PLAN TYPE
                      </Typography>
                      <Typography className={classes.text} color='textSecondary'>
                        EFFECTIVITY DATE
                      </Typography>
                      <Typography className={classes.text} color='textSecondary'>
                        EXPIRATION DATE
                      </Typography>
                    </Typography>
                  </div>
                  <div className={classes.value}>
                    {clientInfo.client.account_status === '' ? (
                      <Typography className={classes.text}>-</Typography>
                    ) : (
                      <Typography className={classes.text}>{clientInfo.client.account_status}</Typography>
                    )}
                    {clientInfo.contract.name === '' ? (
                      <Typography className={classes.text}>-</Typography>
                    ) : (
                      <Typography className={classes.text}>{clientInfo.contract.name}</Typography>
                    )}
                    {clientInfo.contract.plan_type === '' ? (
                      <Typography className={classes.text}>-</Typography>
                    ) : (
                      <Typography className={classes.text}>{clientInfo.contract.type}</Typography>
                    )}
                    {clientInfo.contract.effectivity === '' ? (
                      <Typography className={classes.text}>-</Typography>
                    ) : (
                      <Typography className={classes.text}>
                        {DateFormatterByAbbreviation(clientInfo.contract.effectivity)}
                      </Typography>
                    )}
                    {clientInfo.contract.expiration === '' ? (
                      <Typography className={classes.text}>-</Typography>
                    ) : (
                      <Typography className={classes.text}>
                        {DateFormatterByAbbreviation(clientInfo.contract.expiration)}
                      </Typography>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Paper>
      )}
    </>
  )
}

const ActionTabs = props => {
  const { headCells, users, clientInfo, onShowViewMore, viewMore, setViewMore } = props
  console.log(users)
  return (
    // TODO permissions (if necessary) and Tab Components
    <CustomTabs defaultIndex={0}>
      <CustomTab id='billing-analyst-tab' label='BILLING ANALYST'>
        <BillingAnalystTab
          headCells={headCells}
          users={users}
          onShowViewMore={onShowViewMore}
          viewMore={viewMore}
          setViewMore={setViewMore}
          clientInfo={clientInfo}
        />
      </CustomTab>
      {/* <CustomTab id='billing-cycle-tab' label='BILLING CYCLE'>
        <BillingCycleTab />
      </CustomTab> */}
      <CustomTab id='billing-instructions-tab' label='BILLING INSTRUCTIONS'>
        <BillingInstructionsTab />
      </CustomTab>
    </CustomTabs>
  )
}

export default function AssignClientPage (props) {
  const initialValue = {
    show: false,
    title: '',
    subtitle: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'row.client.account_name',
    order: true
  }

  const [viewMore, setViewMore] = React.useState(initialValue)

  const handleOnShowViewMore = analyst => () => {
    const title = analyst.first_name + ' ' + analyst.last_name
    const count = analyst.assignedCount
    const subtitle = `Billing Analyst for ${count} Accounts`
    const limit = 5
    const page = 1

    setViewMore({ ...initialValue, show: true })

    // TODO API CALL

    const results = analyst.clients

    setViewMore(state => ({ ...state, data: results, title, subtitle, count, limit, page }))
  }

  const handleOnViewMoreChange = params => {
    const _params = {
      category: viewMore.category,
      limit: viewMore.limit,
      page: viewMore.page,
      ...params
    }

    // TODO API CALL
    const results = [
      {
        account: 'Global IT',
        payment_term: 'Monthly',
        billing_cycle: 'CY02 - 10th and 25th of the month'
      },
      {
        account: 'Hitair, Inc',
        payment_term: 'Quarterly',
        billing_cycle: 'CY04 - 15th of the month'
      },
      {
        account: 'World Class Software',
        payment_term: 'Annually',
        billing_cycle: 'CY04 - 15th of the month'
      }
    ]

    const count = 3
    const limit = 5
    const page = 1

    setViewMore(state => ({ ...state, data: results, count, limit, page }))
  }

  const [users, setUsers] = React.useState({})

  const [clientContract, setClientContract] = React.useState({ data: 0 })

  const [pagination, setPagination] = React.useState({
    count: 0,
    rowsPerPage: 10,
    page: 0,
    onChangePage: (event, newPage) => {
      setPagination(state => ({ ...state, page: newPage }))
    },
    onChangeRowsPerPage: event => {
      setPagination(state => ({ ...state, rowsPerPage: +event.target.value }))
    }
  })

  React.useEffect(() => {
    const { _id, account_number } = props.match.params

    PageTitleService('Assign Billing Analyst')
    BreadcrumbsService.setSelectedItems(['dashboard', 'clients', 'assign', `assignId:${account_number}`])

    /* Client Info */
    Promise.all([BillingSupervisorServices.getClientLedgerToSetup({ _id })]).then(values => {
      const client_contract = values[0].data.data
      setClientContract(prevState => ({
        ...prevState,
        data: client_contract
      }))
    })

    /* Table */
    Promise.all([
      // BillingSupervisorServices.getClientConfig({
      //   sort: 'effectivity_date'
      // })
      BillingSupervisorServices.getUsersByRole('BILLING_ANALYST')
    ]).then(values => {
      const userData = values[0].data.data[0]
      // const page = clients.data.data.page
      const page = 3
      setUsers(prevState => ({
        ...prevState,
        data: userData
      }))
      setPagination(prevState => ({
        ...prevState,
        count: userData.length,
        rowsPerPage: 10,
        page: page - 1
      }))
    })

    // ])
  }, [])

  return (
    <>
      <ClientInformation clientInfo={clientContract.data} classes={classes} />
      <ActionTabs
        headCells={header_items}
        users={users}
        clientInfo={clientContract.data}
        onShowViewMore={handleOnShowViewMore}
        viewMore={viewMore}
        setViewMore={setViewMore}
      />
    </>
  )
}
