import React from 'react'

import { Link } from 'react-router-dom'
import { Typography } from '@material-ui/core'

import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import Popover from '@material-ui/core/Popover'

import CloseIcon from '@material-ui/icons/Close'
import ReplyIcon from '@material-ui/icons/Reply'
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle'

import Button from '@material-ui/core/Button'
import IconButton from '@material-ui/core/IconButton'
import HeaderDetails from '../../Components/HeaderDetails/HeaderDetails'
import FormDrawer from '../../Components/FormDrawer/FormDrawer'

import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import Submit from 'StandardComponents/Submit/Submit'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEdit, faAngleDown } from '@fortawesome/free-solid-svg-icons'

import ContentHeader from 'StandardComponents/ContentHeader/ContentHeader'

// Temp TableForMemo
import TempTableForMemo from './TempTableForMemo'

import classes from './CreateTransactionCreditNotePage.module.scss'

export default function (props) {
  PageTitleService()
  BreadcrumbsService.setSelectedItems(['dashboard', 'createTransaction'])
  const [dropDownAnchor, setDropDownAnchor] = React.useState(null)
  const [popOverAnchor, setPopOverAnchor] = React.useState(null)
  const [currentSelected, setCurrentSelected] = React.useState('CY06 - 20th of the month')
  const open = Boolean(dropDownAnchor)
  const id = open ? 'simple-popover' : undefined
  const [submit, setSubmit] = React.useState(false)

  const handleDropDownClick = event => {
    setDropDownAnchor(event.currentTarget)
  }

  const handleDropDownClose = () => {
    setDropDownAnchor(null)
  }

  const handlePopOverClick = event => {
    setPopOverAnchor(event.currentTarget)
  }

  const handlePopOverClose = () => {
    setPopOverAnchor(null)
  }

  const handleOnSubmit = () => {
    setSubmit(true)
  }

  return (
    <div>
      <HeaderDetails>
        <Typography id='header-details-create-transaction' variant='h5'>
          Create Transaction
        </Typography>
      </HeaderDetails>
      <FormDrawer
        drawerHeight={'595px'}
        drawerHeaderContent={''}
        drawerContent={
          <div>
            <Typography id='drawer-corporate-name-field' variant='h6'>
              Happy Innovative, Inc
              <RemoveCircleIcon className={classes['remove-icon']} />
            </Typography>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-number-field' color='textSecondary'>
                ACCOUNT NO.
              </Typography>
              <Typography id='drawer-account-number-value'>6112-0954-0299</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-status-field' color='textSecondary'>
                ACCOUNT STATUS
              </Typography>
              <Typography id='drawer-account-status-value'>Active</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-tax-type-field' color='textSecondary'>
                TAX TYPE
              </Typography>
              <Typography id='drawer-tax-type-value'>VAT</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-billing-cyle-field' color='textSecondary'>
                BILLING CYCLE
              </Typography>
              <Typography id='drawer-billing-cyle-value'>448-49-7280</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-corporate-tin-field' color='textSecondary'>
                CORPORATE TIN
              </Typography>
              <Typography id='drawer-corporate-tin-value'>593-18-7261</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-payment-term-field' color='textSecondary'>
                PAYMENT TERM
              </Typography>
              <Typography id='drawer-payment-term-value'>Monthly</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-contract-plan-type-field' color='textSecondary'>
                CONTRACT PLAN TYPE
              </Typography>
              <Typography id='drawer-contract-plan-type-value'>TPA</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-effectivity-date-field' color='textSecondary'>
                EFFECTIVITY DATE
              </Typography>
              <Typography id='drawer-effectivity-date-value'>Jan 18, 2019</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-expiration-date-field' color='textSecondary'>
                EXPIRATION DATE
              </Typography>
              <Typography id='drawer-expiration-date-value'>Jan 18, 2020</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-location-field' color='textSecondary'>
                LOCATION
              </Typography>
              <Typography id='drawer-location-value'>
                <Typography>5659 10F Rim Lichen Morning Street,</Typography>
                <Typography>Bacidiaceae Subdivision, Amarillo City</Typography>
                <Typography>Brgy. Fordem, Texas 1792</Typography>
              </Typography>
            </div>
          </div>
        }
      >
        {
          <div>
            <div className={classes['export-modify-billing-cycle-wrapper']}>
              <div className={classes['export-modify-content']}>
                <ReplyIcon id='export-icon' className={classes['export-icon']} />
                <Link id='export-link' to={`/`} className={classes['export-link']}>
                  Export
                </Link>
                <FontAwesomeIcon
                  id='modify-billing-cycle-icon'
                  className={classes['modify-billing-cycle-icon']}
                  icon={faEdit}
                />
                <Link
                  id='modify-billing-cycle-link'
                  onClick={handleDropDownClick}
                  className={classes['modify-billing-cycle-link']}
                >
                  Modify Billing Cycle
                </Link>
              </div>
            </div>
            <Popover
              id={id}
              className={classes['pop-over-wrapper']}
              open={open}
              anchorEl={dropDownAnchor}
              onClose={handleDropDownClose}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'right'
              }}
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right'
              }}
            >
              <div className={classes['popover-container']}>
                Select Billing Cycle to include this transaction{' '}
                <IconButton
                  id='pop-over-close-button'
                  aria-label='close'
                  className={classes['pop-over-close-button']}
                  onClick={handleDropDownClose}
                >
                  <CloseIcon id='pop-over-close-icon' />
                </IconButton>
                <Button
                  id='modify-billing-cycle-button'
                  className={classes['modify-billing-cycle-button']}
                  aria-controls='customized-menu'
                  aria-haspopup='true'
                  onClick={handlePopOverClick}
                  endIcon={
                    <FontAwesomeIcon className={classes['modify-billing-cycle-button-icon']} icon={faAngleDown} />
                  }
                >
                  {currentSelected}
                </Button>
                <Menu
                  id='modify-billing-cycle-menu'
                  className={classes['modify-billing-cycle-menu']}
                  anchorEl={popOverAnchor}
                  keepMounted
                  open={Boolean(popOverAnchor)}
                  onClose={handlePopOverClose}
                  anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right'
                  }}
                  transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right'
                  }}
                >
                  <MenuItem
                    id='modify-billing-cycle-item-1'
                    onClick={e => {
                      setCurrentSelected('CY01 - 1st and 15th of the month')
                      handlePopOverClose()
                    }}
                  >
                    CY01 - 1st and 15th of the month
                  </MenuItem>
                  <MenuItem
                    id='modify-billing-cycle-item-2'
                    onClick={e => {
                      setCurrentSelected('CY02 - 10th and 25th of the month')
                      handlePopOverClose()
                    }}
                  >
                    CY02 - 10th and 25th of the month
                  </MenuItem>
                  <MenuItem
                    id='modify-billing-cycle-item-3'
                    onClick={e => {
                      setCurrentSelected('CY03 - 30th of the month')
                      handlePopOverClose()
                    }}
                  >
                    CY03 - 30th of the month
                  </MenuItem>
                  <MenuItem
                    id='modify-billing-cycle-item-4'
                    onClick={e => {
                      setCurrentSelected('CY04 - 15th of the month')
                      handlePopOverClose()
                    }}
                  >
                    CY04 - 15th of the month
                  </MenuItem>
                  <MenuItem
                    id='modify-billing-cycle-item-5'
                    onClick={e => {
                      setCurrentSelected('CY05 - 7th of the month')
                      handlePopOverClose()
                    }}
                  >
                    CY05 - 7th of the month
                  </MenuItem>
                  <MenuItem
                    id='modify-billing-cycle-item-6'
                    onClick={e => {
                      setCurrentSelected('CY06 - 20th of the month')
                      handlePopOverClose()
                    }}
                  >
                    CY06 - 20th of the month
                  </MenuItem>
                </Menu>
                <div className={classes['pop-over-apply-close-button-container']}>
                  <div className={classes['pop-over-close-button-wrapper']}>
                    <Button
                      id='pop-over-cancel-button'
                      variant='contained'
                      className={classes['pop-over-cancel-button']}
                      onClick={handleDropDownClose}
                    >
                      Cancel
                    </Button>
                  </div>
                  <div className={classes['pop-over-apply-button-wrapper']}>
                    <Button
                      id='pop-over-apply-button'
                      variant='contained'
                      className={classes['pop-over-apply-button']}
                      // onClick={handleOnSubmit}
                    >
                      Apply
                    </Button>
                  </div>
                </div>
              </div>
            </Popover>
            <ContentHeader summaryFields={['Sub Total', 'Tax Amount', 'Total Amount']} summaryValues={[0, 0, 0]}>
              <div className={classes['content-header-details']}>
                <div className={classes.fields}>
                  <Typography id='content-details-credit-memo-field' variant='h6' color='textSecondary'>
                    CREDIT MEMO
                  </Typography>
                  <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                    TRANSACTION DATE
                  </Typography>
                  <Typography id='content-details-ticket-number-field' color='textSecondary'>
                    TICKET NO
                  </Typography>
                  <Typography id='content-details-soa-number-field' color='textSecondary'>
                    SOA NO
                  </Typography>
                </div>
                <div className={classes.values}>
                  <Typography id='content-details-credit-memo-value' variant='h6'>
                    CM000001
                    <RemoveCircleIcon className={classes['remove-icon']} />
                  </Typography>
                  <Typography id='content-details-trasaction-date-value'>Dec 01, 2019</Typography>
                  <Typography id='content-details-ticket-number-value'>-</Typography>
                  <Typography id='content-details-soa-number-value'>
                    SOA 001-000000007 <RemoveCircleIcon className={classes['remove-icon']} />
                  </Typography>
                </div>
              </div>
            </ContentHeader>
            <TempTableForMemo search />
          </div>
        }
        <div className={classes['button-wrapper']}>
          <div className={classes.button}>
            <Link id='cancel-link' to={`/`} className={classes['cancel-link']}>
              <Button id='cancel-button' variant='contained' className={classes['cancel-button']}>
                Cancel
              </Button>
            </Link>
            <Button
              id='submit-button'
              variant='contained'
              className={classes['submit-button']}
              onClick={handleOnSubmit}
            >
              Save and Submit
            </Button>
          </div>
        </div>
      </FormDrawer>
      {/* <div className={classes.test}>
        <div className={classes.button}>
          <Link id='close-link' to={`/`}>
            <Button id='close-button' variant='contained' className={classes['close-button']}>
              Cancel
            </Button>
          </Link>
          <Button id='submit-button' variant='contained' className={classes['submit-button']} onClick={handleOnSubmit}>
            Save and Submit
          </Button>
        </div>
      </div> */}
      <Submit page='credit' openDialog={submit} setOpenDialog={setSubmit} />
    </div>
  )
}
