import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

// Components
import TextField from 'StandardComponents/TextField/CustomTextField'
import DisapproveValidator from './DisapproveValidator'

// Styles
import classes from './Disapprove.module.scss'

const SuccessDialogDisapprove = props => {
  const {
    setOpenDisapproveDialog,
    page,
    openSuccessDisapproveDialog,
    setOpenSuccessDisapproveDialog,
    submitFunction
  } = props

  const handleOnCloseSuccessDialog = () => {
    setOpenSuccessDisapproveDialog(false)
  }

  const submitDisapproveFunction = () => {
    setOpenSuccessDisapproveDialog(false)
    setOpenDisapproveDialog(false)
    submitFunction()
  }

  return (
    <Dialog
      fullWidth
      maxWidth='xs'
      onClose={handleOnCloseSuccessDialog}
      aria-labelledby='customized-dialog-title'
      open={openSuccessDisapproveDialog}
      className={classes.root}
      PaperProps={{ style: { height: '35%', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <IconButton
            aria-label='close'
            id='success-close-button'
            className={classes.button}
            onClick={handleOnCloseSuccessDialog}
          >
            <CloseIcon />
          </IconButton>
          <Typography variant='h6' id={`${page}-title`} className={classes.title}>
            Disapprove Statement of Account
          </Typography>
        </div>
      </DialogTitle>
      <DialogContent>
        Statement of Account (SOA) was successfully disapproved by the Supervisor and Manager.
        <div className={classes.button}>
          <Button
            variant='contained'
            id={`${page}-ok-button`}
            className={classes['button-positive']}
            onClick={submitDisapproveFunction}
          >
            OK
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  )
}

export default function CustomizedDialogs (props) {
  const { openDisapproveDialog, setOpenDisapproveDialog, submitFunction } = props
  const [openSuccessDisapproveDialog, setOpenSuccessDisapproveDialog] = React.useState(false)
  const [errors, setErrors] = React.useState({
    disapprove: []
  })
  const [value, setValue] = React.useState({ remarks: '' })
  const [allowedFields, setAllowedFields] = React.useState({})
  const _allowedFields = {
    remarks: ''
  }

  setTimeout(() => {
    // setValue(_allowedFields)
    setAllowedFields(_allowedFields)
  }, 10)

  const handleOnchange = value => {
    setValue({
      remarks: value
    })
  }

  const handleOnClose = () => {
    setOpenDisapproveDialog(false)
  }

  const doValidatedisapprove = value => {
    let { error } = DisapproveValidator({
      remarks: value.remarks
    })

    if (error) {
      error = { ...error }
      console.group('Base Error')
      console.log(error)
      console.groupEnd()
      setErrors(state => ({ disapprove: state.disapprove, ...error }))
      return !!error
    }
  }

  const doValidateAll = data => {
    const disapprove = doValidatedisapprove(data)

    setErrors(state => ({ ...state, disapprove }))
    return !!disapprove
  }

  const onShowSuccessDialog = () => {
    const data = {}
    for (let f in allowedFields) data[f] = value[f]
    const hasError = doValidateAll(data)
    if (hasError) return
    setOpenSuccessDisapproveDialog(true)
  }
  console.log(value)

  return (
    <React.Fragment>
      <Dialog
        fullWidth
        maxWidth='xs'
        onClose={handleOnClose}
        aria-labelledby='customized-dialog-title'
        open={openDisapproveDialog}
        className={classes.root}
        PaperProps={{ style: { height: '400px', display: 'flex' } }}
      >
        <DialogTitle disableTypography className={classes['header-container']}>
          <div className={classes.header}>
            <IconButton aria-label='close' id='confirm-close-button' className={classes.button} onClick={handleOnClose}>
              <CloseIcon />
            </IconButton>
            <Typography variant='h6' id='confirm-title' className={classes.title}>
              Disapprove Statement of Account
            </Typography>
          </div>
        </DialogTitle>
        <DialogContent id='soa-content'>
          Are you sure you want to disapprove the Statement of Account? this will return the SOA to the Billing Analyst
          for correction.
          <br />
          <br />
          Select "No" to return to the current screen.
          <br />
          <br />
          <TextField
            id='remarks-approve-id'
            defaultValue={value.remarks}
            onChange={event => handleOnchange(event.target.value)}
            label='Reason for Disapproving SOA*'
            required
            error={errors.remarks}
          />
          <br />
          <br />
          <div className={classes.button}>
            <Button
              variant='outlined'
              id='soa-cancel-button'
              className={classes['button-negative']}
              onClick={handleOnClose}
            >
              No
            </Button>
            <Button
              variant='contained'
              id='soa-disapprove-button'
              className={classes['button-positive']}
              onClick={onShowSuccessDialog}
            >
              Yes
            </Button>
          </div>
        </DialogContent>
      </Dialog>
      {openSuccessDisapproveDialog === true ? (
        <SuccessDialogDisapprove
          openSuccessDisapproveDialog={openSuccessDisapproveDialog}
          setOpenSuccessDisapproveDialog={setOpenSuccessDisapproveDialog}
          submitFunction={submitFunction}
          setOpenDisapproveDialog={setOpenDisapproveDialog}
        />
      ) : (
        ''
      )}
    </React.Fragment>
  )
}
