import React from 'react'
import { Link, useHistory } from 'react-router-dom'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import Alert from 'SharedComponents/Alert/Alert'
// import Button from '@material-ui/core/Button'
import { makeStyles } from '@material-ui/core/styles'
import PropTypes from 'prop-types'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Box from '@material-ui/core/Box'

import HeaderDetails from 'StandardComponents/HeaderDetails/HeaderDetails'
import FormDrawer from 'StandardComponents/FormDrawer/FormDrawer'
import FormDrawerSupervisor from '../../Components/SoaSupervisorFormDrawer/FormDrawer'
import BIR from 'StandardComponents/BIR/BIR'
import Corporate from 'StandardComponents/Corporate/Corporate'
import Attachment from 'StandardComponents/Attachment/Attachment'
import Submit from 'StandardComponents/Submit/Submit'
import Analyze from 'StandardComponents/Analyze/Analyze'
import TabPanel from '../../Components/TabPanelComponent/TabPanelComponent'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import BillingAnalystServices from 'Services/Api/BillingAnalystServices'
import BillingSupervisorServices from 'Services/Api/BillingSupervisorServices'

// CSS
import classes from './SOAPage.module.scss'
import { Divider } from '@material-ui/core'

function a11yProps (index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`
  }
}

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: '#F5F7FB'
  },
  tabs: {
    fontSize: 12
  },
  indicator: {
    backgroundColor: '#3AB77D'
  },
  // button: {
  selected: {
    backgroundColor: 'yellow'
  }
  // }
}))

export default function SOAPage (props) {
  const { soaNumber, type } = props.match.params
  const [ledger, setLedger] = React.useState({})
  const [soaPage, setSOAPage] = React.useState(false)
  const tabClasses = useStyles()
  const [openDialog, setOpenDialog] = React.useState(false)
  const [activeStep, setActiveStep] = React.useState(0)
  const [value, setValue] = React.useState(0)
  const history = useHistory()

  const handleChange = (event, newValue) => {
    setValue(newValue)
  }

  PageTitleService()

  React.useEffect(() => {
    Promise.all([BillingAnalystServices.getClientSOA({ soaNumber })]).then(values => {
      setLedger(state => ({ ...state, details: values[0].data.data[0] }))
      setSOAPage(true)
      console.log(values[0])
    })
  }, [activeStep])

  if (ledger.details) {
    BreadcrumbsService.setSelectedItems(['ledgers', `ledgerId:${ledger.details.account_name.toUpperCase()}`])
  }

  const voidFunction = soaNumber => {
    BillingSupervisorServices.setClientSOAVoid({ soaNumber: soaNumber }).then(res => {
      console.group('Void SOA')
      console.log(res)
      console.groupEnd()
    })
    history.push('/')
  }

  const getSteps = () => {
    return ['Analyze Statement', 'Statement (BIR)', 'Statement (Corporate)', 'Attachment', 'Submit']
  }

  const getStepContent = props => {
    const { stepIndex, activeStep } = props
    switch (stepIndex) {
      case 0:
        return <Analyze values={{ soa: ledger.details, type: type }} />
      case 1:
        return <BIR soaNumber={soaNumber} />
      case 2:
        return <Corporate soaNumber={soaNumber} />
      case 3:
        return activeStep ? (
          <Attachment submit={{ activeStep: activeStep, type: type }} />
        ) : (
          <Attachment soaNumber={soaNumber} />
        )
    }
  }

  const steps = getSteps()

  const handleNext = () => {
    setActiveStep(prevActiveStep => prevActiveStep + 1)
  }

  const handleBack = () => {
    setActiveStep(prevActiveStep => prevActiveStep - 1)
    if (activeStep === 0) history.push('/')
  }

  const handleReset = () => {
    setActiveStep(0)
  }
  // for saving
  const save = soaNumber => {
    BillingAnalystServices.saveClientSOA({ soaNumber: soaNumber }).then(res => {
      console.group('Save SOA')
      console.log(res)
      console.groupEnd()
    })
    history.push('/')
  }

  const { role } = JSON.parse(localStorage.getItem('me'))

  return (
    <div className={classes.root}>
      <React.Fragment>
        <HeaderDetails ledger={ledger.details} activeStep={activeStep} steps={steps} type={'analyst'} />
        {role === 'BILLING_ANALYST' && (
          <FormDrawer
            voided={() => voidFunction(soaNumber)}
            type={type}
            soaPage={soaPage}
            getStepContent={getStepContent}
            activeStep={activeStep}
            steps={steps}
            save={() => save(soaNumber)}
            nextStep={handleNext}
            stepBack={handleBack}
            reset={handleReset}
            drawerHeight={'570px'}
            drawerHeaderContent={
              <div className={tabClasses.root}>
                <Tabs
                  value={value}
                  onChange={handleChange}
                  aria-label='simple tabs example'
                  classes={{ indicator: tabClasses.indicator }}
                >
                  <Tab className={tabClasses.tabs} label='Billing Instructions' {...a11yProps(0)} />
                  <Tab className={tabClasses.tabs} label='Terms & Conditions' {...a11yProps(1)} />
                </Tabs>
              </div>
            }
            drawerContent={<TabPanel soa={ledger.details} value={value} />}
          />
        )}

        {role === 'BILLING_SUPERVISOR' && (
          <FormDrawerSupervisor
            type={type}
            soaPage={soaPage}
            getStepContent={getStepContent}
            activeStep={activeStep}
            steps={steps}
            openDialog={openDialog}
            setOpenDialog={setOpenDialog}
            nextStep={handleNext}
            stepBack={handleBack}
            voided={() => voidFunction(soaNumber)}
            reset={handleReset}
            drawerHeight={'570px'}
            drawerHeaderContent={
              <div className={tabClasses.root}>
                <Tabs
                  value={value}
                  onChange={handleChange}
                  aria-label='simple tabs example'
                  classes={{ indicator: tabClasses.indicator }}
                >
                  <Tab className={tabClasses.tabs} label='Billing Instructions' {...a11yProps(0)} />
                  <Tab className={tabClasses.tabs} label='Terms & Conditions' {...a11yProps(1)} />
                </Tabs>
              </div>
            }
            drawerContent={<TabPanel soa={ledger.details} value={value} />}
          />
        )}
      </React.Fragment>
      {/* <React.Fragment>
        <HeaderDetails ledger={ledger.details} activeStep={activeStep} steps={steps} type={'analyst'} />
        <FormDrawer
          voidFunction={() => voidFunction(soaNumber)}
          type={type}
          soaPage={soaPage}
          getStepContent={getStepContent}
          activeStep={activeStep}
          steps={steps}
          save={() => save(soaNumber)}
          nextStep={handleNext}
          stepBack={handleBack}
          reset={handleReset}
          drawerHeight={'570px'}
          drawerHeaderContent={
            <div className={tabClasses.root}>
              <Tabs
                value={value}
                onChange={handleChange}
                aria-label='simple tabs example'
                classes={{ indicator: tabClasses.indicator }}
              >
                <Tab className={tabClasses.tabs} label='Billing Instructions' {...a11yProps(0)} />
                <Tab className={tabClasses.tabs} label='Terms & Conditions' {...a11yProps(1)} />
              </Tabs>
            </div>
          }
        />
      </React.Fragment> */}
    </div>
  )
}
