export default function TreeViewTable (props) {
  const {
    headCells,
    children,
    handleRequestSort,
    handleCollapseEvent,
    order,
    orderBy,
    pagination,
    sorter,
    isViewTree,
    actionText,
    actionTo
  } = props

  React.useEffect(() => {}, [])

  return (
    <React.Fragment>
      <TableContainer component={Paper}>
        <Table size='small' aria-label='a dense table'>
          <TableHead className={classes.headerbar}>
            <TableRow>
              {isViewTree ? (
                <TableCell>
                  <IconButton onClick={handleCollapseEvent}>
                    <Exposure className={classes.sidescroll_button} />
                  </IconButton>
                </TableCell>
              ) : (
                ''
              )}
              {headCells.map(headCell => (
                <TableCell
                  key={headCell.id}
                  sortDirection={orderBy === headCell.id ? order : false}
                  width={headCell.width}
                  className={classes.headerCell}
                  // align={headCell.center ? 'center' : headCell.numeric ? 'right' : 'left'}
                  // padding={headCell.disablePadding ? 'none' : 'default'}
                >
                  {sorter ? (
                    <TableSortLabel
                      active={orderBy === headCell.id}
                      direction={order}
                      onClick={event => handleRequestSort(event, headCell.id)}
                    >
                      {headCell.label}
                      {orderBy === headCell.id ? (
                        <span>{order === 'desc' ? 'sorted descending' : 'sorted ascending'}</span>
                      ) : null}
                    </TableSortLabel>
                  ) : headCell.label === '>' ? (
                    <IconButton onClick={handleCollapseEvent}>
                      <ArrowForwardIos />
                    </IconButton>
                  ) : (
                    headCell.label
                  )}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          {children}
        </Table>
        {pagination && <TablePagination rowsPerPageOptions={[5, 10, 25]} component='div' {...pagination} />}
        {/* pagination && (<TablePagination rowsPerPageOptions={[5, 10, 25]} component='div' {...pagination} />) */}
      </TableContainer>
    </React.Fragment>
  )
}
