// Services
import BillingSOAReportService from '../../Services/Api/BillingSOAReportService'
export default function downloadServices (props) {
  const { soaNumber, options, type } = props
  switch (options) {
    case 'bir':
      BillingSOAReportService.getReport({ type: type, soaNumber: soaNumber }).then(response => {
        const url = URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', 'soa-bir.pdf')
        document.body.appendChild(link)
        link.click()
      })
      break

    case 'corporate':
      BillingSOAReportService.getReport({ type: type, soaNumber: soaNumber }).then(response => {
        const url = URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', 'soa-corporate.pdf')
        document.body.appendChild(link)
        link.click()
      })
      break

    case 'attachment':
      BillingSOAReportService.getReport({ type: type, soaNumber: soaNumber }).then(response => {
        const url = URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', 'soa-attachment.pdf')
        document.body.appendChild(link)
        link.click()
      })
      break
  }
}
