import React from 'react'
import Paper from '@material-ui/core/Paper'

import classes from './HeaderDetails.module.scss'

export default function HeaderDetails (props) {
  const { children } = props

  return (
    <div>
      <Paper className={classes['header-wrapper']}>
        <div className={classes['header-details']}>{children}</div>
      </Paper>
    </div>
  )
}
