import React from 'react'
import { Link, useHistory } from 'react-router-dom'

import { Typography } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import VisibilityIcon from '@material-ui/icons/Visibility'
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle'
import ReplyIcon from '@material-ui/icons/Reply'

import * as moment from 'moment'

import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'

import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { CurrencyFormatter } from 'Utilities/TextFormatter'

import HeaderDetails from '../../Components/HeaderDetails/HeaderDetails'
import FormDrawer from '../../Components/FormDrawer/FormDrawer'
import ContentHeader from 'StandardComponents/ContentHeader/ContentHeader'
import CustomEnhancedTable from '../../Components/CustomEnhancedTable/CustomEnhancedTable'

// import BillingAnalystServices from 'Services/Api/BillingAnalystServices'
import BillingInvoiceServices from 'Services/Api/BillingClientInvoicesServices'

import classes from './InvoicePendingPage.module.scss'

const tableHeaders = [
  {
    id: 'pending-invoice-transaction-type',
    numeric: false,
    disablePadding: false,
    label: 'Choose Billing Code & Type',
    center: true
  },
  {
    id: 'pending-invoice-member-account-id',
    numeric: false,
    disablePadding: false,
    label: 'Enter Member ID',
    center: true
  },
  {
    id: 'pending-invoice-member-name',
    numeric: false,
    disablePadding: false,
    label: 'Enter Member Name',
    center: true
  },
  {
    id: 'pending-invoice-description',
    numeric: false,
    disablePadding: false,
    label: 'Enter Description',
    center: true
  },
  {
    id: 'pending-invoice-amount',
    numeric: false,
    disablePadding: false,
    label: 'Enter Taxable Amount',
    center: true
  },
  {
    id: '',
    numeric: false,
    disablePadding: false,
    label: '',
    center: true
  }
]

export default function InvoiceToBeRevised (props) {
  PageTitleService()
  const history = useHistory()

  const transaction_number = props.match.params.transactionNumber
  const [approvedInvoice, setApprovedInvoice] = React.useState({
    data: {}
  })
  const [disabledSave, setDisabledSave] = React.useState(false)
  console.log(transaction_number)
  const [invoiceDetail, setInvoiceDetail] = React.useState({})
  React.useEffect(() => {
    Promise.all([BillingInvoiceServices.getTobeRevisedInvoice({ transaction_number })]).then(value => {
      const [invoice] = value
      console.log(invoice.data.data)
      setApprovedInvoice(prevState => ({
        ...prevState,
        data: invoice.data.data
      }))
    })

    // TODO API CALL FOR CURRENT INVOICE TRANSACTIONS
    Promise.all([BillingInvoiceServices.getInvoiceDetails({ transaction_number })]).then(value => {
      const [detail] = value
      setInvoiceDetail(prevState => ({
        ...prevState,
        data: detail.data.data
      }))
    })
  }, [])

  const transactionInfo = approvedInvoice.data
  const clientInfo = approvedInvoice.data.client
  const billingInfo = approvedInvoice.data.billing
  const contractInfo = approvedInvoice.data.contract
  const summaryInfo = approvedInvoice.data.summary
  const dateInfo = approvedInvoice.data.date
  const invoiceInfo = invoiceDetail.data
  function handleClose () {
    history.push('/')
  }

  function handleSaved (transaction_number) {
    BillingInvoiceServices.saveRevisedInvoices({ transaction_number: transaction_number })
    setDisabledSave(true)
  }

  function handleSubmitted (transaction_number) {
    BillingInvoiceServices.submitRevisedInvoices({ transaction_number: transaction_number })
    history.push('/')
  }

  BreadcrumbsService.setSelectedItems([
    'dashboard',
    'invoice',
    `invoiceID:${clientInfo && clientInfo.account_name + ' - Invoice to be Revised'}`
  ])

  return (
    <div>
      <HeaderDetails>
        <Typography id='header-details-corporate-name' variant='h5'>
          {clientInfo && clientInfo.account_name} - Invoice
        </Typography>
        <Typography id='header-details-account-number' color='textSecondary'>
          ACCOUNT NUMBER {clientInfo && clientInfo.account_number}
        </Typography>
      </HeaderDetails>
      <FormDrawer
        drawerHeight={'570px'}
        drawerContent={
          <div>
            <Typography variant='h6'>{clientInfo && clientInfo.account_name}</Typography>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-number-field' color='textSecondary'>
                ACCOUNT NO.
              </Typography>
              <Typography id='drawer-account-number-value'>{clientInfo && clientInfo.account_number}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-status-field' color='textSecondary'>
                ACCOUNT STATUS
              </Typography>
              <Typography id='drawer-account-status-value'>{clientInfo && clientInfo.account_status}</Typography>
            </div>
            <div className={classes['drawer-content']}>
              <Typography id='drawer-payment-term-field' color='textSecondary'>
                PAYMENT TERM
              </Typography>
              <Typography id='drawer-payment-term-value'>{contractInfo && contractInfo.payment_term}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-tax-type-field' color='textSecondary'>
                TAX TYPE
              </Typography>
              <Typography id='drawer-tax-type-value'>{clientInfo && clientInfo.tax_type}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-billing-cyle-field' color='textSecondary'>
                BILLING CYCLE
              </Typography>
              <Typography id='drawer-billing-cyle-value'>
                {billingInfo && billingInfo.cycle.code + ' - ' + billingInfo.cycle.description}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-corporate-tin-field' color='textSecondary'>
                CORPORATE TIN
              </Typography>
              <Typography id='drawer-corporate-tin-value'>{clientInfo && clientInfo.tin}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-payment-term-field' color='textSecondary'>
                PAYMENT TERM
              </Typography>
              <Typography id='drawer-payment-term-value'>{contractInfo && contractInfo.payment_terms}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-contract-plan-type-field' color='textSecondary'>
                CONTRACT PLAN TYPE
              </Typography>
              <Typography id='drawer-contract-plan-type-value'>{contractInfo && contractInfo.type}</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-effectivity-date-field' color='textSecondary'>
                EFFECTIVITY DATE
              </Typography>
              <Typography id='drawer-effectivity-date-value'>
                {moment(contractInfo && contractInfo.effectivity).format('MMM, DD, YYYY')}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-expiration-date-field' color='textSecondary'>
                EXPIRATION DATE
              </Typography>
              <Typography id='drawer-expiration-date-value'>
                {moment(contractInfo && contractInfo.expiration).format('MMM, DD, YYYY')}
              </Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-location-field' color='textSecondary'>
                LOCATION
              </Typography>
              <Typography id='drawer-location-value'>{clientInfo && clientInfo.location}</Typography>
            </div>
          </div>
        }
      >
        <div className={classes['export-view-billing-cycle-wrapper']}>
          <div className={classes['export-view-content']}>
            <ReplyIcon id='export-icon' className={classes['export-icon']} />
            <Link id='export-link' to={`/`} className={classes['export-link']}>
              Export
            </Link>
            <VisibilityIcon id='view-billing-icon' className={classes['view-icon']} />
            <Link id='view-billing-link' to={`/`} className={classes['view-link']}>
              View Billing Cycle
            </Link>
          </div>
        </div>
        <ContentHeader
          summaryFields={['Total Taxable Amount', 'Total Tax (VAT) Amount', 'Total Amount']}
          summaryValues={[
            summaryInfo && summaryInfo.sub_total,
            summaryInfo && summaryInfo.tax_amount,
            summaryInfo && summaryInfo.sub_total + summaryInfo.tax_amount
          ]}
        >
          <div className={classes['content-header-details']}>
            <div className={classes.fields}>
              <Typography id='content-details-invoice-number-field' variant='h6' color='textSecondary'>
                INVOICE NO.
              </Typography>
              <Typography id='content-details-trasaction-date-field' color='textSecondary'>
                TRANSACTION DATE
              </Typography>
              <Typography id='content-details-ticket-number-field' color='textSecondary'>
                TICKET NO
              </Typography>
            </div>
            <div className={classes.values}>
              <Typography id='content-details-invoice-number-value' variant='h6'>
                {transaction_number}{' '}
                <RemoveCircleIcon id='view-billing-icon' color='secondary' className={classes['remove-icon']} />
              </Typography>
              <Typography id='content-details-trasaction-date-value'>
                {moment(dateInfo && dateInfo.created).format('MMM DD, YYYY')}
              </Typography>
              <Typography id='content-details-ticket-number-value' style={{ width: '100px' }}>
                {transactionInfo && transactionInfo.case_number}
              </Typography>
            </div>
          </div>
        </ContentHeader>
        <CustomEnhancedTable search className={classes.table} tableHeaders={tableHeaders} tableHeight={'325px'}>
          <TableBody>
            {console.log(invoiceInfo)}
            {invoiceInfo &&
              invoiceInfo.map((detail, detailIndex) => (
                <TableRow hover tabIndex={-1} key={detailIndex}>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.billingCodeType}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.memberAccountId}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.memberName}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='center'
                  >
                    {detail.description}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='right'
                  >
                    {CurrencyFormatter(detail.taxableAmount)}
                  </TableCell>
                  <TableCell
                    // id={`${content[0]}-${contentsIndex}`}
                    key={detailIndex}
                    align='right'
                  >
                    <RemoveCircleIcon id='view-billing-icon' color='secondary' className={classes['remove-icon']} />
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </CustomEnhancedTable>
        <div className={classes.button}>
          <Button
            id='close-button'
            variant='contained'
            color='primary'
            className={classes['button-negative']}
            onClick={e => handleClose()}
          >
            Close
          </Button>
          <Button
            id='save-button'
            variant='contained'
            color='primary'
            className={classes['button-positive']}
            disabled={disabledSave}
            onClick={e => handleSaved(transaction_number)}
          >
            Save
          </Button>
          <Button
            id='submitted-link'
            variant='contained'
            className={classes['button-positive']}
            onClick={e => handleSubmitted(transaction_number)}
          >
            {' '}
            Submit
          </Button>
        </div>
      </FormDrawer>
    </div>
  )
}
