// Services
import BillingSOAReportService from '../../Services/Api/BillingSOAReportService'

export default function getPreviewReport (props) {
  const { options, soaNumber, setFile, type } = props
  function getReports ({ type, soaNumber }) {
    BillingSOAReportService.getReport({ type: type, soaNumber: soaNumber }).then(response => {
      const blob = new Blob([response.data])
      setFile(blob)
    })
  }
  switch (options) {
    case 'bir':
      getReports({ type: type, soaNumber: soaNumber })
      break
    case 'corporate':
      getReports({ type: type, soaNumber: soaNumber })
      break
    case 'attachment':
      getReports({ type: type, soaNumber: soaNumber })
      break
  }
}
