import React from 'react'

import { makeStyles } from '@material-ui/core/styles'
import Drawer from '@material-ui/core/Drawer'
import IconButton from '@material-ui/core/IconButton'
import CssBaseline from '@material-ui/core/CssBaseline'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCaretSquareLeft, faCaretSquareDown } from '@fortawesome/free-regular-svg-icons'

import ContentPage from '../ContentPage/ContentPage'

const drawerWidth = 300
const useStyles = makeStyles(theme => ({
  drawerWrapper: {
    display: 'flex'
  },
  menuButton: {
    backgroundColor: '#F5F7FB',
    color: '#9ea3ac'
  },
  iconWrapper: {
    padding: '0px',
    marginRight: '10px',
    marginTop: '-20px',
    color: '#9ea3ac !important'
  },
  hide: {
    display: 'none',
    color: '#9ea3ac'
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    height: 'auto'
  },
  drawerPaper: {
    width: drawerWidth,
    position: 'relative',
    zIndex: 1
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
    backgroundColor: '#F5F7FB',
    padding: '0px'
  },
  drawerHeaderContent: {
    padding: '0px',
    flex: 1
  },
  drawerContentContainer: {
    flexGrow: 1,
    backgroundColor: '#F5F7FB'
  }
}))

export default function FormDrawer (props) {
  const { children, drawerContent, drawerHeaderContent, drawerHeight } = props

  const classes = useStyles()
  const [open, setOpen] = React.useState(true)

  const handleDrawerOpen = () => {
    setOpen(true)
  }

  const handleDrawerClose = () => {
    setOpen(false)
  }

  return (
    <div className={classes.drawerWrapper}>
      <CssBaseline />
      <div>
        <IconButton
          color='inherit'
          aria-label='open drawer'
          onClick={handleDrawerOpen}
          edge='start'
          className={`${classes.menuButton} && ${open ? classes.hide : ''}`}
        >
          <FontAwesomeIcon icon={faCaretSquareDown} />
        </IconButton>
      </div>
      <Drawer
        className={classes.drawer}
        variant='persistent'
        anchor='left'
        open={open}
        classes={{
          paper: classes.drawerPaper
        }}
        style={{ height: `${drawerHeight || 'auto'}` }}
      >
        <div className={classes.drawerHeader}>
          <div className={classes.drawerHeaderContent}>{drawerHeaderContent}</div>
          <div className={classes.drawerHeaderColseButton}>
            <IconButton id='drawer-arrow-icon-button' className={classes.menuButton} onClick={handleDrawerClose}>
              <FontAwesomeIcon icon={faCaretSquareLeft} />
            </IconButton>
          </div>
        </div>
        <div className={classes.drawerContentContainer}>{drawerContent}</div>
      </Drawer>
      <ContentPage open={open}>{children}</ContentPage>
    </div>
  )
}
