  var cheaders3 = [{ header_text: 'Corporate Account' }, { header_text: 'Billing Schedule' }]

  var mheaders1 = [{ header_text: 'Member Name' }, { header_text: 'Corporate Account' }, { header_text: 'Due Date' }]

  var mheaders2 = [{ header_text: 'Member Name' }, { header_text: 'Corporate Account' }, { header_text: 'Bill Type' }]

  var headers_accinfo = [{ header_text: 'Corporate Account' }]

  var headers_tinfo = [{ header_text: 'Corporate Account' }, { header_text: 'Transaction type' }]

  var headers_vsinfo = [{ header_text: 'Corporate Account' }, { header_text: 'SOA No.' }]

