import React from 'react'
import moment from 'moment'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Collapse from '@material-ui/core/Collapse'

import ExpansionPanel from '@material-ui/core/ExpansionPanel'
import IconButton from '@material-ui/core/IconButton'
import AddIcon from '@material-ui/icons/Add'
import RemoveIcon from '@material-ui/icons/Remove'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'

import classes from './TreeViewTable.module.scss'
import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

export default function NormalRow (props) {
  const { index, table_columns, data, id, to, param, actionTo, style } = props

  return (
    <React.Fragment>
      <TableRow key={index}>
        <TableCell id={`account-id-${index}`} />
        {table_columns.map((column, iteration) => (
          <TableCell className={classes.cell}>
            {moment(data[column], moment.ISO_8601).isValid() ? DateFormatterByAbbreviation(data[column]) : data[column]}
          </TableCell>
        ))}
        <TableCell className={classes.cell}>
          <Link id to={param == null ? actionTo : actionTo.concat('/', data[param])} style={style}>
            View
          </Link>
        </TableCell>
      </TableRow>
    </React.Fragment>
  )
}
