import React, { useRef } from 'react'
import { useHistory } from 'react-router-dom'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import Checkbox from '@material-ui/core/Checkbox'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import moment from 'moment'
import ability from '../../../SecurityConfig/rbac-config.js'

// Components
import ConfirmDialog from '../ConfirmDialog/ConfirmDialog'
import DropDown from 'StandardComponents/DropDown/DropDown'

// Services
import BillingAnalystServices from 'Services/Api/BillingAnalystServices'

// Styles
import classes from './BillingCycleTab.module.scss'

let billingCycleSelected = []
let hasConflict = false
let dateIsEqual = false
let initialDateAndEffectivityIsEqual = false

const FooterBar = props => {
  const { clientInfo, processingRule } = props

  const [assign, setAssign] = React.useState({ show: false, title: '', text: '', text2: '' })

  const history = useHistory()

  const handleOnClickAssign = () => {
    console.log(hasConflict)
    const title = hasConflict || dateIsEqual ? 'Pre-Generate Bill Conflict' : 'Billing Cycle Successfully Saved'
    const text = hasConflict
      ? dateIsEqual
        ? "There is a conflict on the first instance of the pre-generate (SOA) days of the selected billing cycle with the account's effectivity date."
        : initialDateAndEffectivityIsEqual
          ? 'There is a conflict on the first instance of the selected billing cycle with the contract effectivity date.'
          : 'The first instance of the pre-generate bill (SOA) days of the selected billing cycle has already past.'
      : 'The billing cycle has been saved sucessfully.'
    const text2 = hasConflict
      ? dateIsEqual || initialDateAndEffectivityIsEqual
        ? 'Please note that the system will perform the pre-generation of SOA for this account on the next scheduled pre-generation date.'
        : 'Please note that the system will only perform an immediate bill run for the initial transactions of the account.'
      : ''

    BillingAnalystServices.updateBillingCycle({
      clientId: clientInfo._id,
      data: {
        id: billingCycleSelected[0]._id,
        ruleExcludeZero: processingRule,
        preGenEqualConflict: dateIsEqual,
        preGenConflict: hasConflict
      }
    }).then(res => {
      setAssign(state => ({ ...state, show: true, title, text, text2 }))
    })
  }

  const handleOnCLickCancel = () => {
    setAssign(state => ({ ...state, show: false }))
  }

  const handleOnCLickOK = () => {
    setAssign(state => ({ ...state, show: false }))
    history.push('/')
  }
  console.log(history)
  const handleOnCLickCancelCycle = () => {
    if (history.location.hash.replace(/[#/]/g, '') === 'from_modal') {
      history.push('/#Client-Ledger-Setup')
    } else {
      history.push('/')
    }
  }

  // this will check if user has an access to this page!
  // if (ability.can('update', 'set-billing-cycle')) {
  //   console.log('Has access to this page')
  // } else {
    // redirect if has no access
  //   history.replace('/404')
  // }

  return (
    <React.Fragment>
      <div className={classes.button}>
        <Button onClick={handleOnCLickCancelCycle} variant='contained' className={classes['button-negative']}>
          Cancel
        </Button>

        <Button variant='contained' className={classes['button-positive']} onClick={handleOnClickAssign}>
          Save
        </Button>
        <ConfirmDialog
          preGenConflict={hasConflict}
          positive_label={'OK'}
          negative_label={'Cancel'}
          assign={assign}
          setAssign={setAssign}
          onCancel={handleOnCLickCancel}
          onOK={handleOnCLickOK}
        />
      </div>
    </React.Fragment>
  )
}

export default function AnalyzeClientPage (props) {
  const { clientInfo } = props

  const [selectedBillingCycle, setSelectedBillingCycle] = React.useState('')
  const [showInfo, setShowInfo] = React.useState(false)
  const [checkProcessingRule, setCheckProcessingRule] = React.useState(true)
  const [billingCycles, setBillingCycles] = React.useState([])

  const infoRef = useRef()

  const handleOnChangeBillingCycle = e => {
    const { value } = e.target.value
    billingCycleSelected = billingCycles.filter(cycle => cycle.code === e.target.value)
    const effectivityDate = moment(clientInfo.contract.effectivity).subtract(
      parseInt(clientInfo.contract.effectivity.substring(11, 13)),
      'h'
    )._d

    try {
      const billingCycleFrequency = billingCycleSelected[0].frequency
      let possibleInitialDate = moment(effectivityDate).date(billingCycleFrequency.criterias[0].criteria)._d
      console.log(billingCycleFrequency.name)
      console.log(new Date(possibleInitialDate).toISOString() < new Date(effectivityDate).toISOString())

      // If the possible initial date is less than the effectivity date, jump to next criteria
      if (new Date(possibleInitialDate).toISOString() < new Date(effectivityDate).toISOString()) {
        for (let i = 0; i < billingCycleFrequency.criterias.length; i++) {
          if (new Date(possibleInitialDate).toISOString() < new Date(effectivityDate).toISOString()) {
            possibleInitialDate = moment(effectivityDate).date(billingCycleSelected[i].frequency.criterias[0].criteria)
              ._d
          }
        }
        if (new Date(possibleInitialDate).toISOString() < new Date(effectivityDate).toISOString()) {
          possibleInitialDate = moment(new Date(possibleInitialDate)).add(1, 'M')._d
        }
      }
      const preGen = moment(new Date(possibleInitialDate).toISOString()).subtract(
        billingCycleSelected[0].pre_generate_formula.unit,
        'd'
      )._d

      console.log(new Date(preGen).toISOString())
      console.log(new Date(possibleInitialDate).toISOString())
      console.log(new Date(effectivityDate).toISOString())

      // Conflict if pregenerated date has passed, pregenerated date is equal to effectivity and possible initial date is equal to effectivity of the contract
      hasConflict =
        new Date(preGen).toISOString() < new Date(effectivityDate).toISOString() ||
        new Date(preGen).toISOString() === new Date(effectivityDate).toISOString()
      // Instance that pregenerated is equal to effectivity of contract
      dateIsEqual = new Date(preGen).toISOString() === new Date(effectivityDate).toISOString()
      // Instance if possible initial date is equal to effectivity date of contract
      initialDateAndEffectivityIsEqual =
        new Date(possibleInitialDate).toISOString() === new Date(effectivityDate).toISOString()
      console.log(hasConflict)
      console.log(dateIsEqual)
      console.log(initialDateAndEffectivityIsEqual)
    } catch (error) {
      console.log('undefined error in selecting billing cycle')
    }

    try {
      const ref = infoRef
      const info = () => {
        ref.current.innerText = `${billingCycleSelected[0].pre_generate_formula.unit +
          ' ' +
          billingCycleSelected[0].pre_generate_formula.period +
          ' ' +
          'before bill run schedule'}`
      }
      info()
    } catch (error) {}
    return setSelectedBillingCycle(value)
  }

  const handleOnChangeProcessingRule = e => {
    if (!e.target.checked) setCheckProcessingRule(false)
    else setCheckProcessingRule(true)
  }

  const [pagination, setPagination] = React.useState({
    count: 0,
    rowsPerPage: 10,
    page: 0,
    onChangePage: (event, newPage) => {
      setPagination(state => ({ ...state, page: newPage }))
    },
    onChangeRowsPerPage: event => {
      setPagination(state => ({ ...state, rowsPerPage: +event.target.value }))
    }
  })

  React.useEffect(() => {
    BillingAnalystServices.getBillingCycleList()
      .then(response => {
        console.log(response)
        setBillingCycles(response.data)
      })
      .catch(error => {
        console.log(error)
      })
  }, [])

  return (
    <React.Fragment>
      <Typography variant='subtitle2' style={{ fontWeight: 'bold' }} className={classes.title}>
        Select Billing Cycle
      </Typography>
      <FormControlLabel
        className={classes['dropdown-option']}
        control={<DropDown onChange={handleOnChangeBillingCycle} items={billingCycles} setShowInfo={setShowInfo} />}
      />
      <br />
      {showInfo && (
        <div className={classes['show-info']}>
          <div className={classes['content-header-details']}>
            <Typography
              id='drawer-premium-percentage-field'
              style={{ fontWeight: 'bold' }}
              className={classes.fields}
              variant='subtitle2'
            >
              Pre-generate Bill (SOA)
            </Typography>
            <Typography id='drawer-premium-percentage-value' className={classes.values} variant='body2'>
              <div ref={infoRef}>
                {billingCycleSelected[0].pre_generate_formula.unit +
                  ' ' +
                  billingCycleSelected[0].pre_generate_formula.period +
                  ' '}
                before bill run schedule
              </div>
            </Typography>
          </div>
          <div className={classes['content-header-details']}>
            <Typography
              id='drawer-ibnr-percentage-field'
              style={{ fontWeight: 'bold' }}
              className={classes.fields}
              variant='subtitle2'
            >
              Bill (SOA) Due Date
            </Typography>
            <Typography id='drawer-ibnr-percentage-value' className={classes.values} variant='body2'>
              after 30 days
            </Typography>
          </div>
        </div>
      )}
      <div className={classes['footer-divider']} />
      <Typography variant='subtitle2' className={classes.title}>
        Processing Rules
      </Typography>

      <FormControlLabel
        control={
          <Checkbox
            checked={checkProcessingRule}
            onChange={handleOnChangeProcessingRule}
            className={checkProcessingRule ? classes.checkbox_option : null}
            value
            style={{ color: 'gray', maxWidth: '300px' }}
            color='default'
          />
        }
        label={
          <Typography variant='body2' className={classes.title}>
            Do not include in printing Statement of Accounts (SOA) with zero (0) Total Amount due
          </Typography>
        }
      />
      <div className={classes['footer-divider']} />
      <FooterBar
        clientInfo={clientInfo}
        billingCycle={selectedBillingCycle}
        processingRule={checkProcessingRule}
        className={classes['form-bar']}
      />
    </React.Fragment>
  )
}
