import React from 'react'
import Paper from '@material-ui/core/Paper'
import Stepper from '@material-ui/core/Stepper'
import Step from '@material-ui/core/Step'
import StepLabel from '@material-ui/core/StepLabel'
import Button from '@material-ui/core/Button'
import * as moment from 'moment'

import PropTypes from 'prop-types'
import { makeStyles, withStyles } from '@material-ui/core/styles'
import clsx from 'clsx'
import StepConnector from '@material-ui/core/StepConnector'

// CSS
import classes from './HeaderDetails.module.scss'
import { Typography } from '@material-ui/core'

export default function HeaderDetails (props) {
  const { ledger, activeStep, steps, type = '' } = props

  const QontoConnector = withStyles({
    alternativeLabel: {
      top: 10,
      left: 'calc(-50% + 5px)',
      right: 'calc(50% + 5px)'
    },
    active: {
      '& $line': {
        borderColor: '#3ab77d'
      }
    },
    completed: {
      '& $line': {
        borderColor: '#3ab77d'
      }
    },
    line: {
      borderColor: '#eaeaf0',
      borderTopWidth: 3,
      borderRadius: 1
    }
  })(StepConnector)

  const useQontoStepIconStyles = makeStyles({
    root: {
      color: '#eaeaf0',
      display: 'flex',
      height: 22,
      alignItems: 'center'
      // fontSize: 10
    },
    active: {
      color: '#3ab77d'
    },
    circle: {
      width: 10,
      height: 10,
      borderRadius: '50%',
      backgroundColor: 'currentColor'
    },
    completed: {
      color: '#3ab77d',
      zIndex: 1
      // fontSize: 18
    },
    label: {
      backgroundColor: 'powderBlue'
    }
  })

  function QontoStepIcon (props) {
    const classes = useQontoStepIconStyles()
    const { active, completed } = props

    return (
      <div
        className={clsx(classes.root, {
          [classes.active]: active
        })}
      >
        {completed ? (
          <div className={`${classes.completed} && ${classes.circle} && ${classes.active}`} />
        ) : (
          <div className={classes.circle} />
        )}
      </div>
    )
  }

  QontoStepIcon.propTypes = {
    active: PropTypes.bool,
    completed: PropTypes.bool
  }

  return (
    <div>
      {type === 'analyst' ? (
        <React.Fragment>
          <div>
            <Paper className={classes.root}>
              {ledger && (
                <div className={classes.container}>
                  <div className={classes.details}>
                    <Typography variant='h5' id='ledger-name'>
                      {ledger.account_name} - Statement of Account
                    </Typography>
                    <Typography id='ledger-account-number'>
                      ACCOUNT NUMBER {ledger.journal.client.account_number}
                    </Typography>
                    {ledger.journal && ledger.journal.contract ? (
                      <Typography id='ledger-effectivity-expiration'>
                        {moment(ledger.journal.contract.effectivity.toUpperCase()).format('MMM DD,YYYY')} -{' '}
                        {moment(ledger.journal.contract.expiration.toUpperCase()).format('MMM DD,YYYY')}
                      </Typography>
                    ) : (
                      ''
                    )}
                    {ledger.journal && ledger.journal.contract ? (
                      <Typography id='ledger-payment-terms'>
                        {ledger.journal.contract.payment_term && ledger.journal.contract.payment_term.toUpperCase()}{' '}
                        PAYMENT TERM
                      </Typography>
                    ) : (
                      ''
                    )}
                  </div>
                  <Stepper
                    activeStep={activeStep}
                    alternativeLabel
                    connector={<QontoConnector />}
                    className={classes.stepper}
                  >
                    {steps.map((label, index) => (
                      <Step key={label}>
                        <StepLabel
                          id={`step-label-${index}`}
                          className={classes.label}
                          StepIconComponent={QontoStepIcon}
                        >
                          {label}
                        </StepLabel>
                      </Step>
                    ))}
                  </Stepper>
                </div>
              )}
            </Paper>
          </div>
        </React.Fragment>
      ) : (
        ''
      )}
      {type === 'validated' ? (
        <React.Fragment>
          <div>
            <Paper className={classes.root}>
              {ledger && (
                <div className={classes.container}>
                  <div className={classes.details}>
                    <Typography variant='h5' id='ledger-name'>
                      {ledger.account_name} - Statement of Account
                    </Typography>
                    <Typography id='ledger-account-number'>
                      ACCOUNT NUMBER {ledger.journal.client.account_number}
                    </Typography>
                    {ledger.journal && ledger.journal.contract ? (
                      <Typography id='ledger-effectivity-expiration'>
                        {moment(ledger.journal.contract.effectivity.toUpperCase()).format('MMM DD,YYYY')} -{' '}
                        {moment(ledger.journal.contract.expiration.toUpperCase()).format('MMM DD,YYYY')}
                      </Typography>
                    ) : (
                      ''
                    )}
                    {ledger.journal && ledger.journal.contract ? (
                      <Typography id='ledger-payment-terms'>
                        {ledger.journal.contract.payment_term && ledger.journal.contract.payment_term.toUpperCase()}{' '}
                        PAYMENT TERM
                      </Typography>
                    ) : (
                      ''
                    )}
                  </div>
                  <div className={classes.pre_soa_details}>
                    <Typography variant='h5'>PRE- SOA NO.</Typography>
                    <Typography color='textSecondary'>DATE APPROVE</Typography>
                  </div>
                  {ledger.journal && ledger.journal.soa ? (
                    <div className={classes.values}>
                      <Typography variant='h5'>{ledger.journal.soa.number}</Typography>
                      <Typography color='textSecondary'>
                        {moment(ledger.journal.date.modified).format('MMM DD,YYYY')}
                      </Typography>
                    </div>
                  ) : (
                    ''
                  )}
                  {ledger.journal && ledger.journal.soa ? (
                    <div className={classes.status}>
                      <Typography variant='h5' color='error'>
                        {ledger.journal.soa.status}
                      </Typography>
                    </div>
                  ) : (
                    ''
                  )}
                  {/* <div className={classes.details}>
                    <Typography variant='h5' color='error'>
                      {ledger.journal.soa.status}
                    </Typography>
                  </div> */}
                </div>
              )}
            </Paper>
          </div>
        </React.Fragment>
      ) : (
        ''
      )}
    </div>
  )
}
