import React from 'react'
import Typography from '@material-ui/core/Typography'

import classes from './CustomPropertyText.module.scss'
export default function CustomPropertyText (props) {
  const { label = '', required, readOnly, constainerStyle = {}, error, ...other } = props

  return (
    <div className={classes.root} style={constainerStyle}>
      <div className={classes.label_container}>
        <Typography variant='body1' color='textSecondary' className={classes.label}>
          {label}
        </Typography>
      </div>
      <div className={classes.value_container}>
        <Typography variant='body1' className={classes.value}>
          {props.value}
        </Typography>
      </div>
    </div>
  )
}
