import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'

// Styles
import classes from './ConfirmDialog.module.scss'
import { Typography, DialogContent, Button } from '@material-ui/core'

export default function CustomizedDialogs (props) {
  const { assign, setAssign, onCancel, onOK, negative_label, positive_label, preGenConflict } = props

  return (
    <Dialog maxWidth='xs' open={assign.show} className={classes.root} PaperProps={{ style: { display: 'flex' } }}>
      <DialogTitle disableTypography>
        <Typography>{assign.title}</Typography>
      </DialogTitle>
      <DialogContent>
        <Typography>{assign.text}</Typography>
        <br />
        {preGenConflict ? <Typography>{assign.text2}</Typography> : null}

        <div className={classes.button}>
          {/* {negative_label && preGenConflict ? (
            <Button variant='outlined' className={classes['button-negative']} onClick={onCancel}>
              {negative_label}
            </Button>
          ) : (
            ''
          )} */}
          {positive_label ? (
            <Button variant='contained' className={classes['button-positive']} onClick={onOK}>
              {positive_label}
            </Button>
          ) : (
            ''
          )}
        </div>
      </DialogContent>
    </Dialog>
  )
}
