import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'

// Styles
import classes from './Submit.module.scss'

const SuccessDialog = props => {
  const { page, openSuccessDialog, setOpenSuccessDialog, submitFunction } = props

  const handleOnCloseSuccessDialog = () => {
    setOpenSuccessDialog(false)
    submitFunction()
  }

  return (
    <Dialog
      fullWidth
      maxWidth='xs'
      onClose={handleOnCloseSuccessDialog}
      aria-labelledby='customized-dialog-title'
      open={openSuccessDialog}
      className={classes.root}
      PaperProps={{ style: { height: '35%', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <IconButton
            aria-label='close'
            id='success-close-button'
            className={classes.button}
            onClick={handleOnCloseSuccessDialog}
          >
            <CloseIcon />
          </IconButton>
          <Typography variant='h6' id={`${page}-title`} className={classes.title}>
            {page === 'debit'
              ? 'Debit Note Submitted'
              : page === 'credit'
                ? 'Credit Note Submitted'
                : page === 'soa'
                  ? 'Statement of Account (SOA) Successfully Submitted'
                  : ''}
          </Typography>
        </div>
      </DialogTitle>
      <DialogContent>
        {page === 'debit' ? 'Debit Note ' : page === 'credit' ? 'Credit Note ' : 'Statement of Account (SOA) '}was
        successfully submitted to the Supervisor and Manager for approval.
        <div className={classes.button}>
          <Button
            variant='contained'
            id={`${page}-ok-button`}
            className={classes['button-positive']}
            onClick={handleOnCloseSuccessDialog}
          >
            OK
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  )
}

export default function CustomizedDialogs (props) {
  const { page, openDialog, setOpenDialog, submitFunction } = props
  const [openSuccessDialog, setOpenSuccessDialog] = React.useState(false)

  const handleOnClose = () => {
    setOpenDialog(false)
  }

  const onShowSuccessDialog = () => {
    setOpenDialog(false)
    setOpenSuccessDialog(true)
  }

  return (
    <React.Fragment>
      <Dialog
        fullWidth
        maxWidth='xs'
        onClose={handleOnClose}
        aria-labelledby='customized-dialog-title'
        open={openDialog}
        className={classes.root}
        PaperProps={{ style: { height: '270px', display: 'flex' } }}
      >
        <DialogTitle disableTypography className={classes['header-container']}>
          <div className={classes.header}>
            <IconButton aria-label='close' id='confirm-close-button' className={classes.button} onClick={handleOnClose}>
              <CloseIcon />
            </IconButton>
            <Typography variant='h6' id='confirm-title' className={classes.title}>
              Confirm Submission
            </Typography>
          </div>
        </DialogTitle>
        {page === 'debit' ? (
          <DialogContent id='debit-content'>
            Are you sure you want to submit the debit note? Are you sure you want to submit the debit note? This debit
            note will be sent to both the Billing Supervisor and Billing Manager for approval.
            <div className={classes.button}>
              <Button
                variant='outlined'
                id='debit-cancel-button'
                className={classes['button-negative']}
                onClick={handleOnClose}
              >
                Cancel
              </Button>
              <Button
                variant='contained'
                id='debit-submit-button'
                className={classes['button-positive']}
                onClick={onShowSuccessDialog}
              >
                Submit Debit Note
              </Button>
            </div>
          </DialogContent>
        ) : page === 'credit' ? (
          <DialogContent id='credit-content'>
            Are you sure you want to submit the credit note? Are you sure you want to submit the credit note? This debit
            note will be sent to both the Billing Supervisor and Billing Manager for approval.
            <div className={classes.button}>
              <Button
                variant='outlined'
                id='credit-cancel-button'
                className={classes['button-negative']}
                onClick={handleOnClose}
              >
                Cancel
              </Button>
              <Button
                variant='contained'
                id='credit-submit-button'
                className={classes['button-positive']}
                onClick={onShowSuccessDialog}
              >
                Submit Credit Note
              </Button>
            </div>
          </DialogContent>
        ) : page === 'soa' ? (
          <DialogContent id='soa-content'>
            Are you sure you want to submit the Statement of Account (SOA) and attachment?
            <br />
            <br />
            This SOA and attachment will be sent to both the Billing Supervisor and Billing Manager for approval.
            <div className={classes.button}>
              <Button
                variant='outlined'
                id='soa-cancel-button'
                className={classes['button-negative']}
                onClick={handleOnClose}
              >
                Cancel
              </Button>
              <Button
                variant='contained'
                id='soa-submit-button'
                className={classes['button-positive']}
                onClick={onShowSuccessDialog}
              >
                Submit
              </Button>
            </div>
          </DialogContent>
        ) : (
          ''
        )}
      </Dialog>
      <SuccessDialog
        page={page}
        openSuccessDialog={openSuccessDialog}
        setOpenSuccessDialog={setOpenSuccessDialog}
        submitFunction={submitFunction}
      />
    </React.Fragment>
  )
}
