import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import NumberFormat from 'react-number-format'
import { CurrencyFormatter } from 'Utilities/TextFormatter'
import CircularProgress from '@material-ui/core/CircularProgress'

import classes from './TextFieldCurrency.module.scss'
export default function TextFieldCurrency (props) {
  const { value = 0, label = '', required, currency = true, readOnly, error, loading, onChange, ...other } = props

  const [focus, setFocus] = React.useState()
  const [_error, setError] = React.useState()

  React.useEffect(() => {
    setError()
    if (typeof value === 'string') {
      if (!value.trim()) setError(error)
    }
    if (typeof value === 'number') {
      if (value <= 0) setError(error)
    }
  }, [error, value, loading])

  const handleOnChange = e => {
    setError()
    if (typeof onChange === 'function') onChange({ target: { value: e.floatValue } })
  }

  return (
    <div className={classes.root}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography
            readOnly
            id={props.id}
            variant='subtitle2'
            className={`${classes.label} ${classes['read-only-value']}`}
          >
            {CurrencyFormatter(value)}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper
              className={`${classes['text-field']} ${
                _error ? classes['has-error'] : focus && classes.focus
              } box-shadow-field`}
            >
              <NumberFormat
                id={props.id}
                className={classes.input}
                required
                customInput={InputBase}
                isNumericString
                displayType={'input'}
                thousandSeparator
                decimalScale={2}
                allowEmptyFormatting
                fixedDecimalScale
                allowLeadingZeros={false}
                value={value}
                onValueChange={handleOnChange}
                isAllowed={values => {
                  const { floatValue } = values
                  return floatValue <= 10000000000000
                }}
                autoComplete='off'
                onFocus={() => setFocus(true)}
                onBlur={() => setFocus(false)}
                // {...other}
              />
              {loading ? (
                <div style={{ marginRight: '5px', alignSelf: 'center' }}>
                  <CircularProgress color='inherit' size={20} />
                </div>
              ) : null}
              {currency && (
                <div className={classes.currency}>
                  <Typography variant='subtitle2' className={classes.label}>
                    PHP
                  </Typography>
                </div>
              )}
            </Paper>
            {_error && <span className={classes.error}>{_error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}
