import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Drawer from '@material-ui/core/Drawer'
import CssBaseline from '@material-ui/core/CssBaseline'
import Divider from '@material-ui/core/Divider'
import IconButton from '@material-ui/core/IconButton'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCaretSquareLeft, faCaretSquareDown } from '@fortawesome/free-regular-svg-icons'

import ContentPage from '../../Components/ContentPageComponent/ContentPageComponent'

const drawerWidth = 350
const useStyles = makeStyles(theme => ({
  sideBarWrapper: {
    display: 'flex'
  },
  menuButton: {
    backgroundColor: '#F5F7FB',
    color: 'rgba(0, 0, 0, 0.54)'
  },
  iconWrapper: {
    padding: '0px',
    marginRight: '10px',
    marginTop: '-20px',
    color: 'rgba(0, 0, 0, 0.54) !important'
  },
  hide: {
    display: 'none',
    color: 'rgba(0, 0, 0, 0.54)'
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0
  },
  drawerPaper: {
    width: drawerWidth,
    position: 'relative'
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
    backgroundColor: '#F5F7FB',
    padding: '0px'
  },
  drawerHeaderContent: {
    padding: '0px',
    flex: 1
  },
  drawerContentContainer: {
    flexGrow: 1,
    backgroundColor: '#F5F7FB',
    paddingTop: 7
  }
}))

export default function PersistentDrawerLeft (props) {
  const {
    soaPage,
    activeStep,
    steps,
    nextStep,
    stepBack,
    reset,
    openDialog,
    setOpenDialog,
    voided,
    handleVoid,
    getStepContent,
    drawerContent,
    drawerHeaderContent,
    children,
    drawerHeight,
    // drawerWidth,
    permanent
  } = props
  const classes = useStyles()
  const [open, setOpen] = React.useState(true)

  const handleDrawerOpen = () => {
    setOpen(true)
  }

  const handleDrawerClose = () => {
    setOpen(false)
  }

  return (
    <div className={classes.sideBarWrapper}>
      <CssBaseline />
      {permanent ? (
        ''
      ) : (
        <div>
          <IconButton
            color='inherit'
            aria-label='open drawer'
            onClick={handleDrawerOpen}
            edge='start'
            className={`${classes.menuButton} && ${open ? classes.hide : ''}`}
          >
            <FontAwesomeIcon icon={faCaretSquareDown} />
          </IconButton>
        </div>
      )}
      <Drawer
        className={classes.drawer}
        variant={permanent ? 'permanent' : 'persistent'}
        anchor='left'
        open={open}
        classes={{
          paper: classes.drawerPaper
        }}
        style={{ height: drawerHeight }}
      >
        {permanent ? (
          ''
        ) : (
          <div className={classes.drawerHeader}>
            <div className={classes.drawerHeaderContent}>{drawerHeaderContent}</div>
            <div className={classes.drawerHeaderColseButton}>
              <IconButton id='drawer-arrow-icon-button' className={classes.iconWrapper} onClick={handleDrawerClose}>
                <FontAwesomeIcon icon={faCaretSquareLeft} />
              </IconButton>
            </div>
          </div>
        )}
        <Divider />
        <div className={classes.drawerContentContainer}>{drawerContent}</div>
      </Drawer>
      <ContentPage
        shift={open}
        soaPage={soaPage}
        getStepContent={getStepContent}
        activeStep={activeStep}
        openDialog={openDialog}
        handleVoid={handleVoid}
        voided={voided}
        setOpenDialog={setOpenDialog}
        steps={steps}
        nextStep={nextStep}
        stepBack={stepBack}
        reset={reset}
        content={children != null ? children : null}
      />
    </div>
  )
}
