import React from 'react'
import moment from 'moment'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Collapse from '@material-ui/core/Collapse'

import ExpansionPanel from '@material-ui/core/ExpansionPanel'
import IconButton from '@material-ui/core/IconButton'
import AddIcon from '@material-ui/icons/Add'
import RemoveIcon from '@material-ui/icons/Remove'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'

import classes from './TreeViewTable.module.scss'
import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

export default function CollapsibleRow (props) {
  const {
    index,
    id,
    data,
    objectKey,
    tableColumnOrder,
    subColumnOrder,
    offset,
    collapseAllState,
    collapseFunction,
    actionText,
    actionTo,
    param
  } = props

  const [collapse, setCollapse] = React.useState(collapseAllState)
  const [iconImage, setIconImage] = React.useState(null)
  const add_icon = 'plus-square'
  const remove_icon = 'minus-square'

  const handleTableCollapse = () => {
    if (collapseAllState) {
      if (collapse) {
        setCollapse(collapseAllState)
        setIconImage(remove_icon)
      } else {
        setIconImage(add_icon)
        // setCollapse(!collapse)
      }
    } else {
      if (collapse) {
        setCollapse(collapseAllState)
        setIconImage(add_icon)
      } else {
        setCollapse(!collapse)
        setIconImage(remove_icon)
      }
    }
    collapseFunction(collapse)
    console.log(' set: to', collapse)
  }

  const offsetCells = []
  for (var i = 0; i < offset; i++) {
    offsetCells.push(<TableCell colspan='1' />)
  }

  React.useEffect(() => {
    setCollapse(collapseAllState)
    setIconImage(add_icon)
  }, [])

  return (
    <React.Fragment>
      <TableRow key={index}>
        <TableCell id={`button-${id}`} className={classes.collapse_button}>
          <IconButton onClick={handleTableCollapse}>
            <FontAwesomeIcon icon={iconImage} />
          </IconButton>
        </TableCell>
        {tableColumnOrder.map((rowItem, rowIndex) => (
          <TableCell id={rowIndex} className={classes.cell}>
            {moment(data[rowItem], moment.ISO_8601).isValid()
              ? DateFormatterByAbbreviation(data[rowItem])
              : data[rowItem]}
          </TableCell>
        ))}
        <TableCell>
          <Link
            id={`link-table-${index}`}
            to={param == null ? actionTo : actionTo.concat('/', data[param])}
            style={{ textDecoration: 'none' }}
          >
            {actionText}
          </Link>
        </TableCell>
      </TableRow>
      {/* <Collapse in={collapse.show} className={classes.collapse}> */}
      {(collapseAllState || collapse) &&
        data[objectKey].map((subItem, subIndex) => (
          <TableRow key={index}>
            {subColumnOrder.map((colItem, colindex) =>
              subColumnOrder === '' ? (
                <TableCell />
              ) : (
                <TableCell className={classes.cell}>
                  {moment(subItem[colItem], moment.ISO_8601).isValid()
                    ? DateFormatterByAbbreviation(subItem[colItem])
                    : subItem[colItem]}
                </TableCell>
              )
            )}
            {actionTo && (
              <TableCell className={classes.cell}>
                <Link
                  color='primary'
                  id={`link-table-sub-${index}`}
                  to={param == null ? actionTo : actionTo.concat('/', data[param])}
                  className={classes.link}
                >
                  {actionText}
                </Link>
              </TableCell>
            )}
          </TableRow>
        ))}
      {/* </Collapse> */}
    </React.Fragment>
  )
}
