import React from 'react'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles'
// import Button from '@material-ui/core/Button'

import { DateFormatter } from 'Utilities/TextFormatter'
import CustomTextField from 'SharedComponents/TextField/CustomTextField'
import IconButton from '@material-ui/core/IconButton'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableHead from '@material-ui/core/TableHead'
import TablePagination from '@material-ui/core/TablePagination'
import Button from '@material-ui/core/Button'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import ViewList from '../../Components/ViewList/ViewList'

import TableFooterComponent from 'SharedComponents/TableFooterComponent/TableFooterComponent'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

import BillingSupervisorServices from 'Services/Api/BillingSupervisorServices'

// CSS
import classes from './ClientTransactionSetupPage.module.scss'

const useStyles = makeStyles(theme => ({
  searchAndAddContainer: {
    padding: '0 0 10px 0',
    minWidth: '1060px',

    addButton: {
      width: 200
    }
  },

  table: {
    minWidth: '640px'
  },

  tableCaption: {
    minWidth: '200px'
  }
}))

const search_classes = useStyles

const headCells = [
  {
    id: 'corporate_id',
    numeric: false,
    center: false,
    disablePadding: false,
    label: 'Corporate Account ID'
  },
  {
    id: 'corporate_name',
    numeric: false,
    center: false,
    disablePadding: false,
    label: 'Corporate Account Name'
  },
  {
    id: 'plan_type',
    numeric: false,
    center: true,
    disablePadding: false,
    label: 'Plan Type'
  },
  {
    id: 'effectivity_date',
    numeric: false,
    center: true,
    disablePadding: false,
    label: 'Effectivity Date'
  }
]

export default function ClientTransactionSetupPage (props) {
  // const [] = React.useState(null)
  const [clients, setClients] = React.useState({
    config: { data: [], metadata: String }
  })

  const [pagination, setPagination] = React.useState({
    count: 0,
    rowsPerPage: 10,
    page: 0,
    onChangePage: (event, newPage) => {
      setPagination(state => ({ ...state, page: newPage }))
    },
    onChangeRowsPerPage: event => {
      setPagination(state => ({ ...state, rowsPerPage: +event.target.value }))
    }
  })

  React.useEffect(() => {
    const { corporate_id } = props.match.params
    const current_user_id = JSON.parse(localStorage.getItem('me'))._id
    console.log({ corporate_id })
    PageTitleService(`List of Client Transaction Ledgers to Setup`)
    BreadcrumbsService.setSelectedItems(['dashboard', 'clients', 'ledger'])

    // setBundleNumber(bundle_number)
    // PaymentRequestServices.getPaymentRequest({ bundle_number }).then(
    //   ({ data: res }) => {
    //     const { data } = res
    //     setBundle(data)
    //     getBatches({ bundle_number, bundle: data })
    //   }
    // )

    // const sort = '{effectivity_date: -1}'

    Promise.all([
      BillingSupervisorServices.getClientConfig({
        sort: {
          field: 'effectivity_date',
          direction: '-1'
        }
      })
      // PaymentRequestServices.getClaims({
      //   category: 'payment-request',
      //   range: 'this-week',
      //   sort: 'claim.date.due'g
      // }),
      // PaymentRequestServices.getClaimCount({ category: 'payment-request' })
    ]).then(values => {
      const [clients] = values
      const clientData = clients.data.data.results

      const page = clients.data.data.page
      setClients(prevState => ({
        ...prevState,
        config: {
          data: clientData,
          metadata: page
        }
      }))
      setPagination(prevState => ({
        ...prevState,
        count: clientData.length,
        rowsPerPage: 10,
        page: page - 1
      }))
    })
  }, [])

  console.log(clients)
  const clientList = clients.config.data

  const SearchAndAdd = ({ classes }) => {
    console.log(classes)
    return (
      <Grid item container className={classes.searchBar} alignItems='center' wrap='nowrap'>
        <Grid item xs={12}>
          <SearchTextComponent />
        </Grid>

        <Grid item container className={classes.addButton} justify='flex-end'>
          <Link to='/' color='primary' size='medium'>
            <IconButton>
              <Typography className='text-link underline'>Display Settings</Typography>
            </IconButton>
            {/* <Button color='primary' size='medium'>
              <Typography variant='caption' className='text-link underline'>
                Display Settings
              </Typography>
            </Button> */}
          </Link>
        </Grid>
      </Grid>
    )
  }

  const ClientTable = ({ clientList }) => {
    return (
      <Paper className='table'>
        <Table className={classes.table} aria-label='caption pagination table'>
          <TableHead>
            <TableRow>
              <TableCell>{headCells[0].label}</TableCell>
              <TableCell>{headCells[1].label}</TableCell>
              <TableCell>{headCells[2].label}</TableCell>
              <TableCell>{headCells[3].label}</TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {clientList.map((item, index) => (
              <TableRow key={index}>
                <TableCell>{item.corporate_id}</TableCell>
                <TableCell>{item.corporate_name}</TableCell>
                {item.contract ? <TableCell>{item.contract.plan_type}</TableCell> : ''}
                {item.contract ? <TableCell>{DateFormatter(item.contract.effectivity, 'MMM DD, YYYY')}</TableCell> : ''}
                <Typography>
                  <Link to={`/clients/assign/${item.corporate_id}`}>
                    <Typography variant='caption' className='text-link underline'>
                      Setup
                    </Typography>
                  </Link>
                </Typography>
              </TableRow>
            ))}
            {/* <Alert
                open={openDialog}
                message='Are you sure you want to delete this batch?'
                cancelLabel='No'
                okLabel='Yes'
                onCancel={onCancel}
                onOk={onDelete}
              />
              <Alert
                open={openError}
                message='Delete Error: This Batch has existing claim records'
                cancelLabel='Ok'
                onCancel={onCancel}
              /> */}
          </TableBody>
        </Table>
        {pagination ? (
          <caption className={classes.table_caption}>
            1 - {pagination.count} out of{' '}
            {pagination.count > pagination.rowsPerPage ? pagination.rowsPerPage : pagination.count} Client Ledgers to
            Setup
          </caption>
        ) : (
          ''
        )}
        {pagination ? (
          <TablePagination
            className={classes.table_pagination}
            rowsPerPageOptions={[10, 15, 20]}
            component='div'
            {...pagination}
          />
        ) : (
          ''
        )}
        {/* <TableFooterComponent itemsLength={clients.length} fetching /> */}
      </Paper>
    )
  }

  const search_style = useStyles()

  return (
    <React.Fragment>
      <SearchAndAdd classes={classes.searchBar} />
      <ClientTable clientList={clientList} pagination={pagination} />
    </React.Fragment>
  )
}
