import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TableSortLabel from '@material-ui/core/TableSortLabel'
import { Link } from 'react-router-dom'

// Components
import Table from 'SharedComponents/Table/Table'

// Styles
import classes from './ViewMore.module.scss'

// const headCells = [
//   { id: 'accounts', numeric: false, center: false, disablePadding: true, label: 'Assigned Accounts' },
//   { id: 'terms', numeric: false, center: true, disablePadding: true, label: 'Payment Terms' },
//   { id: 'cycle', numeric: false, center: false, disablePadding: true, label: 'Billing Cycle' }
// ]
const headCells = [{ name: 'Assigned Accounts' }, { name: 'Payment Terms' }, { name: 'Billing Cycle' }]

export default function CustomizedDialogs (props) {
  const { viewMore, setViewMore, onViewMoreChange } = props

  const handleOnClose = () => {
    setViewMore(state => ({ ...state, show: false }))
  }

  const [debounce, setDebounce] = React.useState(null)

  return (
    <Dialog
      fullWidth
      maxWidth='sm'
      onClose={handleOnClose}
      aria-labelledby='customized-dialog-title'
      open={viewMore.show}
      className={classes.root}
      PaperProps={{ style: { height: '100%', display: 'flex' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <IconButton aria-label='close' className={classes.button} onClick={handleOnClose}>
            <CloseIcon />
          </IconButton>
          <Typography variant='h6' className={classes.title}>
            {viewMore.title}
          </Typography>
          <Typography variant='subtitle1' className={classes.subtitle}>
            {viewMore.subtitle}
          </Typography>
        </div>
      </DialogTitle>
      <DialogContent>
        <Table
          TableContainerProps={{ style: { height: '320px', overflow: 'auto', position: 'relative' } }}
          TableProps={{ stickyHeader: true }}
          head={
            <TableRow>
              {headCells.map((headCell, index) => (
                <TableCell key={index}>
                  {/* <TableSortLabel
                    active={viewList.orderBy === headCell.sortField}
                    direction={viewList.order ? 'asc' : 'desc'}
                    onClick={handleOnSort(headCell.sortField)}
                  > */}
                  {headCell.name}
                  {/* </TableSortLabel> */}
                </TableCell>
              ))}
            </TableRow>
          }
          body={viewMore.data.map((row, index) => (
            <TableRow key={index}>
              <TableCell>{row.client.account_name}</TableCell>
              <TableCell>{row.contract.payment_terms}</TableCell>
              <TableCell>
                {row.billing.cycle.code ? row.billing.cycle.code + ' - ' + row.billing.cycle.name : ''}
              </TableCell>
            </TableRow>
          ))}
          count={viewMore.count}
          page={viewMore.page}
          limit={viewMore.limit}
          rowsPerPageOptions={[10, 15, 20]}
          // onChange={onViewListChange}
        />
      </DialogContent>
    </Dialog>
  )
}
