import instance from './instance'
const axios = instance('/b/ci')

export default class BillingClientInvoicesServices {
  static getInvoiceDetails ({ transaction_number }) {
    return axios({
      url: `/billing/invoice-details/${transaction_number}`,
      method: 'GET'
    })
  }

  static getTobeRevisedInvoice ({ transaction_number }) {
    return axios({
      url: `/billing/invoice-get/${transaction_number}`,
      method: 'GET'
    })
  }

  static getTobeRevisedInvoices ({ transaction_status, condition }) {
    return axios({
      url: `/billing/invoice-getall/${transaction_status}/${condition}`,
      method: 'GET'
    })
  }
  static saveRevisedInvoices ({ transaction_number }) {
    return axios({
      url: `/billing/invoice/invoice-save/${transaction_number}`,
      method: 'PUT'
    })
  }

  static submitRevisedInvoices ({ transaction_number }) {
    return axios({
      url: `/billing/invoice/invoice-submit/${transaction_number}`,
      method: 'PUT'
    })
  }

  static getRevisedInvoice ({ transaction_number }) {
    return axios({
      url: `/billing/invoice/${transaction_number}`,
      method: 'GET'
    })
  }

  static assign ({ transaction_number }) {
    return axios({
      url: `/billing/invoice/invoice-assign/${transaction_number}`,
      method: 'PUT'
    })
  }

  static approve ({ transactionNumber }) {
    console.log('??????????????????????????????????', transactionNumber)
    return axios({
      url: `/billing/invoice/invoice-approve/${transactionNumber}`,
      method: 'PUT'
    })
  }

  static disapprove ({ transaction_number }) {
    return axios({
      url: `/billing/invoice/invoice-disapprove/${transaction_number}`,
      method: 'PUT'
    })
  }

  static void ({ transaction_number }) {
    return axios({
      url: `/billing/invoice/invoice-void/${transaction_number}`,
      method: 'PUT'
    })
  }
}
