import React from 'react'
import Paper from '@material-ui/core/Paper'
import Submit from 'StandardComponents/Submit/Submit'
import { useHistory } from 'react-router-dom'

// Components
import PdfViewer from 'StandardComponents/PdfViewer/PdfViewer'
import Approve from 'StandardComponents/Approve/Approve'

// Service
import BillingSOAReportService from '../../Services/Api/BillingSOAReportService'
import BillingAnalystServices from 'Services/Api/BillingAnalystServices'
import BillingSupervisorServices from 'Services/Api/BillingSupervisorServices'

import classes from './Attachment.module.scss'

export default function Attachment (props) {
  const { soaNumber, submit = {} } = props
  const [file, setFile] = React.useState(null)
  const [number, setNumber] = React.useState(soaNumber)
  const [openDialog, setOpenDialog] = React.useState(true)
  const history = useHistory()
  const type = 'list'
  React.useEffect(() => {
    BillingSOAReportService.getReport({ type: type, soaNumber: soaNumber }).then(response => {
      const blob = new Blob([response.data])
      setFile(blob)
      setNumber(soaNumber)
    })
  }, [type])
  const downloadServices = args => {
    const { type } = args
    BillingSOAReportService.getReport({ type: type, soaNumber: soaNumber }).then(response => {
      const url = URL.createObjectURL(new Blob([response.data]))
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', 'soa-list.pdf')
      document.body.appendChild(link)
      link.click()
    })
  }

  const approveFunction = soaNumber => {
    BillingSupervisorServices.setClientSOAApprove({ soaNumber: soaNumber }).then(res => {
      console.group('Void SOA')
      console.log(res)
      console.groupEnd()
    })
    history.push('/')
  }

  const disapproveFunction = soaNumber => {
    BillingSupervisorServices.setClientSOADisapprove({ soaNumber: soaNumber }).then(res => {
      console.group('Void SOA')
      console.log(res)
      console.groupEnd()
    })
    history.push('/')
  }

  const submitFunction = soaNumber => {
    BillingAnalystServices.submitClientSOA({ soaNumber: soaNumber })
    history.push('/')
  }

  return (
    <div className={classes.root}>
      {submit.type === 'supervisor' ? (
        <div>
          {file ? (
            <PdfViewer
              soaNumber={soaNumber}
              options={'list'}
              file={file}
              download={() => downloadServices({ type: 'category' })}
              type={'list'}
            />
          ) : (
            ''
          )}
          {submit.activeStep && (
            <Approve
              page='soa'
              openDialog={openDialog}
              setOpenDialog={setOpenDialog}
              submitFunction={() => approveFunction(number)}
              disapproveFunction={() => disapproveFunction(number)}
            />
          )}
        </div>
      ) : (
        <div>
          {file ? (
            <PdfViewer
              soaNumber={soaNumber}
              options={'list'}
              file={file}
              download={() => downloadServices({ type: 'category' })}
              type={'list'}
            />
          ) : (
            ''
          )}
          {submit.activeStep && (
            <Submit
              page='soa'
              openDialog={openDialog}
              setOpenDialog={setOpenDialog}
              submitFunction={() => submitFunction(number)}
            />
          )}
        </div>
      )}
      {submit.type === 'validated' ? (
        <div>
          {file ? (
            <PdfViewer
              soaNumber={soaNumber}
              options={'list'}
              file={file}
              download={() => downloadServices({ type: 'category' })}
              type={'list'}
            />
          ) : (
            ''
          )}
          {submit.activeStep && history.push('/')}
        </div>
      ) : (
        ''
      )}
    </div>
  )
}
