import React from 'react'

import { Link } from 'react-router-dom'
import { Typography } from '@material-ui/core'

import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { withStyles } from '@material-ui/core/styles'

import RemoveCircleIcon from '@material-ui/icons/RemoveCircle'

import Button from '@material-ui/core/Button'
import HeaderDetails from '../../Components/HeaderDetails/HeaderDetails'
import FormDrawer from '../../Components/FormDrawer/FormDrawer'

import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faAngleDown,
  faFileInvoice,
  faMoneyBillWaveAlt,
  faFileAlt,
  faFileSignature
} from '@fortawesome/free-solid-svg-icons'

// Temp EnhancedSearchComponent
import EnhacedSearchReferenceNo from './EnhacedSearchReferenceNo'

import classes from './CreateTransactionTypePage.module.scss'

const StyledMenu = withStyles({
  paper: {
    // border: '1px solid #d3d4d5'
  }
})(props => (
  <Menu
    elevation={0}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'right'
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'right'
    }}
    {...props}
  />
))

const StyledMenuItem = withStyles(theme => ({
  root: {
    '& .MuiListItemText-primary': {
      fontSize: '13px'
    }
  }
}))(MenuItem)

export default function (props) {
  PageTitleService()
  BreadcrumbsService.setSelectedItems(['dashboard', 'createTransaction'])

  const [dropDownAnchor, setDropDownAnchor] = React.useState(null)
  const [currentSelected, setCurrentSelected] = React.useState(1)

  const handleDropDownClick = event => {
    setDropDownAnchor(event.currentTarget)
  }

  const handleDropDownClose = () => {
    setDropDownAnchor(null)
  }

  const selectedItem = item => {
    switch (item) {
      case 1:
        return (
          <React.Fragment>
            <ListItemIcon>
              <FontAwesomeIcon className={classes['transaction-type-item-icon-invoice']} icon={faFileInvoice} />
            </ListItemIcon>
            <ListItemText primary='Invoice' />
          </React.Fragment>
        )
      case 2:
        return (
          <React.Fragment>
            <ListItemIcon>
              <FontAwesomeIcon
                className={classes['transaction-type-item-icon-credit-memo']}
                icon={faMoneyBillWaveAlt}
              />
            </ListItemIcon>
            <ListItemText primary='Credit Memo' />
          </React.Fragment>
        )
      case 3:
        return (
          <React.Fragment>
            <ListItemIcon>
              <FontAwesomeIcon className={classes['transaction-type-item-icon-debit-note']} icon={faFileAlt} />
            </ListItemIcon>
            <ListItemText primary='Debit Note' />
          </React.Fragment>
        )
      case 4:
        return (
          <React.Fragment>
            <ListItemIcon>
              <FontAwesomeIcon className={classes['transaction-type-item-icon-credit-note']} icon={faFileSignature} />
            </ListItemIcon>
            <ListItemText primary='Credit Note' />
          </React.Fragment>
        )
    }
  }

  return (
    <div>
      <HeaderDetails>
        <Typography id='header-details-create-transaction' variant='h5'>
          Create Transaction
        </Typography>
      </HeaderDetails>
      <FormDrawer
        drawerHeight={'595px'}
        drawerHeaderContent={''}
        drawerContent={
          <div>
            <Typography id='drawer-corporate-name-value' variant='h6'>
              Happy Innovative, Inc
              <RemoveCircleIcon className={classes['remove-icon']} />
            </Typography>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-number-field' color='textSecondary'>
                ACCOUNT NO.
              </Typography>
              <Typography id='drawer-account-number-value'>6112-0954-0299</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-account-status-field' color='textSecondary'>
                ACCOUNT STATUS
              </Typography>
              <Typography id='drawer-account-status-value'>Active</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-tax-type-field' color='textSecondary'>
                TAX TYPE
              </Typography>
              <Typography id='drawer-tax-type-value'>VAT</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-billing-cyle-field' color='textSecondary'>
                BILLING CYCLE
              </Typography>
              <Typography id='drawer-billing-cyle-value'>448-49-7280</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-corporate-tin-field' color='textSecondary'>
                CORPORATE TIN
              </Typography>
              <Typography id='drawer-corporate-tin-value'>593-18-7261</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-payment-term-field' color='textSecondary'>
                PAYMENT TERM
              </Typography>
              <Typography id='drawer-payment-term-value'>Monthly</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-contract-plan-type-field' color='textSecondary'>
                CONTRACT PLAN TYPE
              </Typography>
              <Typography id='drawer-contract-plan-type-value'>TPA</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-effectivity-date-field' color='textSecondary'>
                EFFECTIVITY DATE
              </Typography>
              <Typography id='drawer-effectivity-date-value'>Jan 18, 2019</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-expiration-date-field' color='textSecondary'>
                EXPIRATION DATE
              </Typography>
              <Typography id='drawer-expiration-date-value'>Jan 18, 2020</Typography>
            </div>

            <div className={classes['drawer-content']}>
              <Typography id='drawer-location-field' color='textSecondary'>
                LOCATION
              </Typography>
              <Typography id='drawer-location-value'>
                <Typography>5659 10F Rim Lichen Morning Street,</Typography>
                <Typography>Bacidiaceae Subdivision, Amarillo City</Typography>
                <Typography>Brgy. Fordem, Texas 1792</Typography>
              </Typography>
            </div>
          </div>
        }
      >
        {
          <div className={classes['main-content-wrapper']}>
            <div className={classes['transaction-type-container']}>
              <div className={classes['transaction-type-field']}>
                <Typography id='transaction-type-field' color='textSecondary'>
                  Transaction Type
                </Typography>
              </div>

              <div className={classes['transaction-type-value']}>
                <Button
                  id='transaction-type-button'
                  className={classes['transaction-type-button']}
                  aria-controls='customized-menu'
                  aria-haspopup='true'
                  onClick={handleDropDownClick}
                  endIcon={<FontAwesomeIcon className={classes['transaction-type-button-icon']} icon={faAngleDown} />}
                >
                  {selectedItem(currentSelected)}
                </Button>
                <StyledMenu
                  id='transaction-type-items'
                  anchorEl={dropDownAnchor}
                  keepMounted
                  open={Boolean(dropDownAnchor)}
                  onClose={handleDropDownClose}
                >
                  <StyledMenuItem
                    id='transaction-type-item-1'
                    onClick={e => {
                      setCurrentSelected(1)
                      handleDropDownClose()
                    }}
                  >
                    <ListItemIcon>
                      <FontAwesomeIcon className={classes['transaction-type-item-icon-invoice']} icon={faFileInvoice} />
                    </ListItemIcon>
                    <ListItemText primary='Invoice' />
                  </StyledMenuItem>
                  <StyledMenuItem
                    id='transaction-type-item-2'
                    onClick={e => {
                      setCurrentSelected(2)
                      handleDropDownClose()
                    }}
                  >
                    <ListItemIcon>
                      <FontAwesomeIcon
                        className={classes['transaction-type-item-icon-credit-memo']}
                        icon={faMoneyBillWaveAlt}
                      />
                    </ListItemIcon>
                    <ListItemText primary='Credit Memo' />
                  </StyledMenuItem>
                  <StyledMenuItem
                    id='transaction-type-item-3'
                    onClick={e => {
                      setCurrentSelected(3)
                      handleDropDownClose()
                    }}
                  >
                    <ListItemIcon>
                      <FontAwesomeIcon className={classes['transaction-type-item-icon-debit-note']} icon={faFileAlt} />
                    </ListItemIcon>
                    <ListItemText primary='Debit Note' />
                  </StyledMenuItem>
                  <StyledMenuItem
                    id='transaction-type-item-4'
                    onClick={e => {
                      setCurrentSelected(4)
                      handleDropDownClose()
                    }}
                  >
                    <ListItemIcon>
                      <FontAwesomeIcon
                        className={classes['transaction-type-item-icon-credit-note']}
                        icon={faFileSignature}
                      />
                    </ListItemIcon>
                    <ListItemText primary='Credit Note' />
                  </StyledMenuItem>
                </StyledMenu>
              </div>
            </div>

            <div className={classes['reference-number-container']}>
              <div className={classes['reference-number-field']}>
                <Typography id='reference-number-field' color='textSecondary'>
                  Reference No.
                </Typography>
              </div>
              <div className={classes['reference-number-value']}>
                <EnhacedSearchReferenceNo width={'450px'} />
              </div>
            </div>

            <Link
              id='advanced-search-link'
              className={classes['advanced-search-link']}
              to='/'
              color='primary'
              size='medium'
            >
              Advanced Search
            </Link>
          </div>
        }
        <div className={classes['button-wrapper']}>
          <div className={classes.button}>
            <Link id='cancel-link' to={`/`} className={classes['cancel-link']}>
              <Button id='cancel-button' variant='contained' className={classes['cancel-button']}>
                Cancel
              </Button>
            </Link>
          </div>
        </div>
      </FormDrawer>
    </div>
  )
}
