import React from 'react'
import { Link, useHistory } from 'react-router-dom'
import RadioButton from '@material-ui/core/Radio/Radio'
import Button from '@material-ui/core/Button'

import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'

// Components
import { EllipsisFormatter } from 'Utilities/TextLengthFormatter'
import EnhancedTable from 'SharedComponents/EnhancedTable/EnhancedTable'
import ViewMore from '../ViewMore/ViewMore'
import ConfirmDialog from '../AssignAnalystConfirmDialogue/ConfirmDialog'

// Styles
import classes from './BillingAnalystTab.module.scss'

const AssignClientPageFooter = props => {
  const history = useHistory()
  const { analyst, clientInfo, setAnalyst } = props

  const [assign, setAssign] = React.useState({ show: false, title: '', text: '', text2: '' })

  const handleOnClickAssign = () => {
    const title = 'Assign Client to Billing Analyst'
    const text = 'Are you sure you want to assign the Account to the selected Billing Analyst?'
    const text2 = 'The account will be sent to the assigned Billing Analyst for processing and setup of billing cycle.'

    setAssign(state => ({ ...state, show: true, title, text, text2, analyst: analyst, clientId: clientInfo._id }))
  }

  const handleOnCLickCancel = () => {
    setAssign(state => ({ ...state, show: false }))
    setAnalyst(null)
  }

  const handleOnCLickCancelAssign = () => {
    if (history.location.hash.replace(/[#/]/g, '') === 'from_modal') {
      history.push('/#Assign-Billing-Analyst')
    } else {
      history.push('/')
    }
  }

  return (
    // <React.Fragment>
    <div className={classes.button}>
      <Button variant='contained' className={classes['button-negative']} onClick={handleOnCLickCancelAssign}>
        Cancel
      </Button>
      <Button variant='contained' className={classes['button-positive']} onClick={handleOnClickAssign}>
        Assign
      </Button>
      <ConfirmDialog assign={assign} setAssign={setAssign} onCancel={handleOnCLickCancel} />
    </div>
    // </React.Fragment>
  )
}

export default function BillingAnalystTab (props) {
  const { headCells, users, clientInfo, onShowViewMore, viewMore, setViewMore } = props
  console.log(users)

  const [selectedAnalyst, setSelectedAnalyst] = React.useState(null)
  const handleOnChangeAnalyst = e => {
    setSelectedAnalyst(e.target.value)
  }

  return (
    <>
      <EnhancedTable headCells={headCells}>
        <TableBody>
          {users.data
            ? users.data.results.map((item, index) => (
                <TableRow key={index}>
                  <TableCell>
                    <RadioButton
                      id={`analyst-radio-${item._id}`}
                      color='primary'
                      checked={selectedAnalyst === item._id}
                      onChange={handleOnChangeAnalyst}
                      value={item._id}
                    />
                  </TableCell>
                  <TableCell id={`analyst-name-${index}`}>{item.first_name + ' ' + item.last_name}</TableCell>
                  <TableCell id={`analyst-status-${index}`}>{item.status}</TableCell>
                  <TableCell id={`analyst-assigned-count-${index}`} align='center'>
                    {item.assignedCount}
                  </TableCell>
                  <TableCell id={`analyst-clients-${index}`}>
                    {EllipsisFormatter(
                      item.clients
                        .map(account => {
                          return account.client.account_name + ''
                        })
                        .join(),
                      50
                    )}
                  </TableCell>
                  <TableCell id={`analyst-view-more-${index}`}>
                    <Link className={classes['view-more']} onClick={onShowViewMore(item)}>
                      View More
                    </Link>
                  </TableCell>
                </TableRow>
              ))
            : 'No Data'}
          <div>
            <ViewMore viewMore={viewMore} setViewMore={setViewMore} />
          </div>
        </TableBody>
      </EnhancedTable>
      <AssignClientPageFooter analyst={selectedAnalyst} setAnalyst={setSelectedAnalyst} clientInfo={clientInfo} />
    </>
  )
}
