import React from 'react'
import { makeStyles, useTheme } from '@material-ui/core/styles'

import ContentPage from 'StandardComponents/ContentPageComponent/ContentPageForValidatedSOA'

const drawerWidth = 350
const useStyles = makeStyles(theme => ({
  sideBarWrapper: {
    display: 'flex'
  },
  menuButton: {
    backgroundColor: '#F5F7FB',
    color: 'rgba(0, 0, 0, 0.54)'
  },
  iconWrapper: {
    padding: '0px',
    marginRight: '10px',
    marginTop: '-20px',
    color: 'rgba(0, 0, 0, 0.54) !important'
  },
  hide: {
    display: 'none',
    color: 'rgba(0, 0, 0, 0.54)'
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0
  },
  drawerPaper: {
    width: drawerWidth,
    position: 'relative'
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
    backgroundColor: '#F5F7FB',
    padding: '0px'
  },
  drawerHeaderContent: {
    padding: '0px',
    flex: 1
  },
  drawerContentContainer: {
    flexGrow: 1,
    backgroundColor: '#F5F7FB',
    paddingTop: 7
  }
}))

export default function PersistentDrawerLeft (props) {
  const {
    voidFunction,
    type,
    soaPage,
    activeStep,
    steps,
    nextStep,
    stepBack,
    reset,
    edit,
    save,
    getStepContent,
    children
  } = props
  const classes = useStyles()
  const [open, setOpen] = React.useState(true)

  return (
    <div className={classes.sideBarWrapper}>
      <ContentPage
        voidFunction={voidFunction}
        type={type}
        shift={open}
        soaPage={soaPage}
        getStepContent={getStepContent}
        activeStep={activeStep}
        steps={steps}
        save={save}
        edit={edit}
        nextStep={nextStep}
        stepBack={stepBack}
        reset={reset}
        content={children != null ? children : null}
      />
    </div>
  )
}
