import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import PropTypes from 'prop-types'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Box from '@material-ui/core/Box'
import Typography from '@material-ui/core/Typography'
import DoneIcon from '@material-ui/icons/Done'

import FieldType from 'Constant/TermsAndConditions'
import SearchBox from '../CustomSearchTextComponent/CustomSearchTextComponent'

import classes from './TabPanelComponent.module.scss'
import { CurrencyFormatter } from 'Utilities/TextFormatter'

function TabPanel (props) {
  const { children, value, index, ...other } = props

  return (
    <Typography
      component='div'
      role='tabpanel'
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  )
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired
}

function a11yProps (index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`
  }
}

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: '#F5F7FB'
  },
  tabs: {
    fontSize: 12
  },
  tabContent: {
    marginTop: 20,
    marginBottom: 20
  },
  selected: {
    backgroundColor: 'yellow'
  },
  icon: {
    fontSize: 'smaller'
  }
}))

export default function SimpleTabs (props) {
  const { value, soa } = props

  return (
    <React.Fragment>
      {soa && soa.journal && (
        <div className={classes.root}>
          <TabPanel value={value} index={0}>
            <Typography>
              <div className={classes.tabContent}>
                <SearchBox id='search-billing-instructions' placeholder='Search Billing Instructions' />
              </div>
              <div className={classes.tabContent}>
                <Typography id='drawer-additional-fields' color='textSecondary' variant='caption'>
                  ADDITIONAL FIELDS TO BE INCLUDED IN THE SOA ATTACHMENT
                </Typography>
                <br />
                {soa.journal.contract && soa.journal.contract.soa_attachment_addtl_fields ? (
                  soa.journal.contract.soa_attachment_addtl_fields.map((item, index) => (
                    <div>
                      <Typography
                        id={`drawer-additional-fields-value-${index}`}
                        style={{ fontWeight: 'bold' }}
                        variant='caption'
                      >
                        <DoneIcon style={{ fontSize: 'smaller' }} />
                        {item.field_name}
                      </Typography>
                    </div>
                  ))
                ) : (
                  <Typography id='drawer-additional-fields-value' style={{ fontWeight: 'bold' }} variant='caption'>
                    No additional fields to attach
                  </Typography>
                )}
              </div>

              <div className={classes.tabContent}>
                <Typography id='drawer-soas-generation-field' color='textSecondary' variant='caption'>
                  SOAs GENERATION
                </Typography>
                <br />
                <Typography id='drawer-choose-soas-generation-field' color='textSecondary' variant='caption'>
                  Choose SOA generation and arrange their hierarchy
                </Typography>
                <br />
                {soa.journal.contract && soa.journal.contract.soa_generation ? (
                  soa.journal.contract.soa_generation.map((item, index) => (
                    <div>
                      <Typography
                        id={`drawer-soas-generation-value-${index}`}
                        style={{ fontWeight: 'bold' }}
                        variant='caption'
                      >
                        <DoneIcon style={{ fontSize: 'smaller' }} />
                        {item.field_name}
                      </Typography>
                    </div>
                  ))
                ) : (
                  <Typography id='drawer-soas-generation-value' style={{ fontWeight: 'bold' }} variant='caption'>
                    No required fields for SOA generation
                  </Typography>
                )}
              </div>

              <div className={classes.tabContent}>
                <Typography id='drawer-soas-sorting-field' color='textSecondary' variant='caption'>
                  SOAs SORTING
                </Typography>
                <br />
                <Typography id='drawer-soas-sorting-options-field' color='textSecondary' variant='caption'>
                  Sort SOA according to options and arrange the hierarchy
                </Typography>
                <br />
                {soa.journal.contract && soa.journal.contract.soa_sorting ? (
                  soa.journal.contract.soa_sorting.map((item, index) => (
                    <div>
                      <Typography
                        id={`drawer-soas-sorting-value-${index}`}
                        style={{ fontWeight: 'bold' }}
                        variant='caption'
                      >
                        <DoneIcon style={{ fontSize: 'smaller' }} /> {item.field_name}
                      </Typography>
                    </div>
                  ))
                ) : (
                  <Typography id='drawer-soas-sorting-value' style={{ fontWeight: 'bold' }} variant='caption'>
                    No sorting options added
                  </Typography>
                )}
              </div>

              <div className={classes.tabContent}>
                <Typography id='drawer-soa-address-field' color='textSecondary' variant='caption'>
                  ADDRESS SOA TO
                </Typography>
                <br />
                {soa.journal.contract && soa.journal.contract.soa_send_to && (
                  <Typography id='drawer-soa-address-value' style={{ fontWeight: 'bold' }} variant='caption'>
                    {soa.journal.contract.soa_send_to.name}
                  </Typography>
                )}
              </div>

              <div className={classes.tabContent}>
                <Typography id='drawer-send-soa-field' color='textSecondary' variant='caption'>
                  SEND SOA TO
                </Typography>
                <br />
                {soa.journal.contract && soa.journal.contract.soa_send_to_address && (
                  <Typography id='drawer-send-soa-value' style={{ fontWeight: 'bold' }} variant='caption'>
                    {soa.journal.contract.soa_send_to_address.floor +
                      ' ' +
                      soa.journal.contract.soa_send_to_address.unit +
                      ' ' +
                      soa.journal.contract.soa_send_to_address.bldg_no +
                      ' ' +
                      soa.journal.contract.soa_send_to_address.bldg_name +
                      ' ' +
                      soa.journal.contract.soa_send_to_address.street +
                      ' ' +
                      soa.journal.contract.soa_send_to_address.brgy +
                      ' ' +
                      soa.journal.contract.soa_send_to_address.region +
                      ' ' +
                      soa.journal.contract.soa_send_to_address.province +
                      ' ' +
                      soa.journal.contract.soa_send_to_address.city +
                      ' ' +
                      soa.journal.contract.soa_send_to_address.zip_code}
                  </Typography>
                )}
              </div>

              <div className={classes.tabContent}>
                <Typography id='drawer-send-soa-days-field' color='textSecondary' variant='caption'>
                  SEND SOA ON SPECIFIC DAYS ONLY
                </Typography>
                <br />
                {soa.journal.contract && soa.journal.contract.soa_send_on_days ? (
                  soa.journal.contract.soa_send_on_days.map((item, index) => (
                    <div>
                      <Typography
                        id={`drawer-send-soa-days-value-${index}`}
                        style={{ fontWeight: 'bold' }}
                        variant='caption'
                      >
                        <DoneIcon style={{ fontSize: 'smaller' }} /> {item}
                      </Typography>
                    </div>
                  ))
                ) : (
                  <Typography id='drawer-send-soa-days-value' style={{ fontWeight: 'bold' }} variant='caption'>
                    <DoneIcon style={{ fontSize: 'smaller' }} /> No specific day/s indicated
                  </Typography>
                )}
              </div>
            </Typography>
          </TabPanel>
          <TabPanel value={value} index={1}>
            <Typography>
              <div className={classes.tabContent}>
                <SearchBox id='search-terms-and-conditions' placeholder='Search Terms and Conditions' />
              </div>
              {soa.journal.contract &&
                soa.journal.contract.terms_and_conditions &&
                soa.journal.contract.terms_and_conditions.map((item, index) => (
                  <React.Fragment>
                    <div className={classes.tabContent}>
                      <Typography id={`drawer-field-section-${index}`} color='textSecondary' variant='caption'>
                        {item.field_section.toUpperCase()}
                      </Typography>
                      <br />
                      {item.field_values &&
                        item.field_values.map((v, index) => (
                          <React.Fragment>
                            <Typography
                              id={`drawer-field-name-${index}`}
                              style={{ fontWeight: 'bold' }}
                              variant='caption'
                            >
                              {v.type === FieldType.CURRENCY ? (
                                <div className={classes.currency}>
                                  <div className={classes.fields}>{v.name}</div>
                                  <div className={classes.values}>
                                    {v.values && v.values.map((val, index) => 'PHP' + CurrencyFormatter(val))}
                                  </div>
                                </div>
                              ) : v.type === FieldType.PERCENTAGE ? (
                                <div>
                                  {v.name}
                                  <br />
                                  {v.values && v.values.map((val, index) => val + '%')}
                                </div>
                              ) : v.type === FieldType.CHECKBOX ? (
                                <div>
                                  {v.name}
                                  <br />
                                  <DoneIcon style={{ fontSize: 'smaller' }} />
                                  {v.values && v.values.map((val, index) => val)}
                                </div>
                              ) : v.type === FieldType.NUMBER ? (
                                <div>
                                  {v.name}
                                  <br />
                                  {v.values && v.values.map((val, index) => val)}
                                </div>
                              ) : (
                                <div>
                                  {v.name}
                                  <br />
                                  <DoneIcon style={{ fontSize: 'smaller' }} />
                                  {v.values && v.values.map((val, index) => val)}
                                </div>
                              )}
                            </Typography>
                            <br />
                          </React.Fragment>
                        ))}
                    </div>
                  </React.Fragment>
                ))}
            </Typography>
          </TabPanel>
        </div>
      )}
    </React.Fragment>
  )
}
