import { defineAbility } from '@casl/ability'
import billingData from './data/billing-policies'
import collectionData from './data/collection-policies'
import claimsData from './data/claims-policies'
import partnerData from './data/partner-network-policies'
import User from './data/billing-user-mock.json'
import RbacController from '../../../Controllers/RbacController'

const userPolicies = []

RbacController.getPolicyData().then(res => {
  console.log(res)
})

// Temporary! Mock data used for deriving policies
try {
  const { username } = JSON.parse(localStorage.getItem('me'))
  const userLogged = User.filter(user => user.login.username === username)[0].login.rbac

  for (let i = 0; i < userLogged.length; i++) {
    console.log('test')
    userPolicies.push({
      method: userLogged[i].method,
      element: userLogged[i].element_id,
      policy_id: userLogged[i].policy_id
    })
  }
} catch (error) {}

export default defineAbility((can, cannot) => {
  for (let i = 0; i < userPolicies.length; i++) {
    can(userPolicies[i].method, userPolicies[i].element)
  }
})
