import React from 'react'
import Typography from '@material-ui/core/Typography'
import FormGroup from '@material-ui/core/FormGroup'
import moment from 'moment'

// Components
import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'
import CustomPropertyText from '../../Components/CustomPropertyText/CustomPropertyText'
import FormDrawer from 'StandardComponents/FormDrawer/FormDrawer'
import { CustomTab, CustomTabs } from 'StandardComponents/CustomTabs/CustomTabs'

// Tab Components
import BillingAnalystTab from '../../Components/AnalystTab/BillingAnalystTab'
import BillingCycleTab from '../../Components/BillingCycleTab/BillingCycleTab'
import BillingInstructionsTab from '../../Components/BillingInstructionsTab/BillingInstructionsTab'
import BillingTermsAndConditionsTab from '../../Components/BillingTermsAndConditionsTab/BillingTermsAndConditionsTab'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

import BillingSupervisorServices from 'Services/Api/BillingSupervisorServices'

// CSS
import classes from './AnalyzeClientPage.module.scss'

const ClientInformation = ({ classes, clientInfo, hmoInfo }) => {
  return (
    <React.Fragment className={classes.client_details}>
      {clientInfo && (
        <React.Fragment>
          <Typography variant='h6' className={classes.title}>
            {clientInfo.client.account_name}
          </Typography>

          <Typography className={classes.title} style={{ fontWeight: 'bold', marginTop: '10px' }}>
            Basic Information
          </Typography>

          <CustomPropertyText
            className={classes.customText}
            readOnly='true'
            label='ACCOUNT NO.'
            value={clientInfo.client.account_number}
          />

          <CustomPropertyText readOnly='true' label='ACCOUNT STATUS' value={clientInfo.client.account_status} />

          <CustomPropertyText
            className={classes.customTextComponent}
            readOnly='true'
            label='PAYMENT TERM'
            value={clientInfo.contract.payment_terms}
          />

          <CustomPropertyText
            readOnly='true'
            label='ANNUAL BENEFIT LIMIT'
            value={'Php ' + CurrencyFormatter(clientInfo.contract.annual_benefit_limit)}
          />

          <CustomPropertyText
            className={classes.customTextComponent}
            readOnly='true'
            label='CLIENT SEGMENT'
            value={clientInfo.client.segment}
          />

          <CustomPropertyText readOnly='true' label='TAX TYPE' value={clientInfo.client.tax_type} />

          <CustomPropertyText readOnly='true' label='ACCOUNT TIN' value={clientInfo.client.tin} />

          <Typography className={classes.title} style={{ fontWeight: 'bold', marginTop: '15px' }}>
            HMO Information
          </Typography>

          <CustomPropertyText readOnly='true' label='CONTRACT' value={clientInfo.contract.name} />

          <CustomPropertyText readOnly='true' label='CONTRACT TYPE' value={clientInfo.contract.type} />
          <CustomPropertyText
            readOnly='true'
            label='EFFECTIVITY DATE'
            value={DateFormatterByAbbreviation(
              new Date(
                moment(clientInfo.contract.effectivity).subtract(
                  parseInt(clientInfo.contract.effectivity.substring(11, 13)),
                  'h'
                )
              )
            )}
          />
          <CustomPropertyText
            readOnly='true'
            label='EXPIRATION DATE'
            value={DateFormatterByAbbreviation(
              new Date(
                moment(clientInfo.contract.expiration).subtract(
                  parseInt(clientInfo.contract.expiration.substring(11, 13)),
                  'h'
                )
              )
            )}
          />
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

const ActionTabs = props => {
  const { clientInfo } = props

  return (
    // TODO permissions (if necessary) and Tab Components
    <CustomTabs defaultIndex={1}>
      <CustomTab id='billing-analyst-tab' label='BILLING ANALYST'>
        <BillingAnalystTab />
      </CustomTab>
      <CustomTab id='billing-cycle-tab' label='BILLING CYCLE'>
        <BillingCycleTab clientInfo={clientInfo} />
      </CustomTab>
      <CustomTab id='billing-instructions-tab' label='BILLING INSTRUCTIONS'>
        <BillingInstructionsTab />
      </CustomTab>
      <CustomTab id='billing-ternms-and-conditions-tab' label='BILLING TERMS & CONDITIONS'>
        <BillingTermsAndConditionsTab />
      </CustomTab>
    </CustomTabs>
  )
}

export default function AnalyzeClientPage (props) {
  const [users, setUsers] = React.useState({})

  const [clientContract, setClientContract] = React.useState({ data: 0 })

  const [pagination, setPagination] = React.useState({
    count: 0,
    rowsPerPage: 10,
    page: 0,
    onChangePage: (event, newPage) => {
      setPagination(state => ({ ...state, page: newPage }))
    },
    onChangeRowsPerPage: event => {
      setPagination(state => ({ ...state, rowsPerPage: +event.target.value }))
    }
  })

  React.useEffect(() => {
    const { _id } = props.match.params
    PageTitleService('Setup Billing Cycle')
    // BreadcrumbsService.setSelectedItems(['dashboard', 'clientSetup', 'clientBillingCycle'])

    /* Client Info */
    Promise.all([BillingSupervisorServices.getClientLedgerToSetup({ _id })]).then(values => {
      const client_contract = values[0].data.data

      setClientContract(prevState => ({
        ...prevState,
        data: client_contract
      }))
    })

    /* Table */
    Promise.all([
      // BillingSupervisorServices.getClientConfig({
      //   sort: 'effectivity_date'
      // })
      BillingSupervisorServices.getUsersByRole('BILLING_ANALYST')
    ]).then(values => {
      const userData = values[0].data.data[0]
      // const page = clients.data.data.page
      const page = 3
      setUsers(prevState => ({
        ...prevState,
        data: userData
      }))
      setPagination(prevState => ({
        ...prevState,
        count: userData.length,
        rowsPerPage: 10,
        page: page - 1
      }))
    })
  }, [])

  return (
    <React.Fragment>
      <FormDrawer
        permanent
        drawerWidth='500'
        drawerContent={<ClientInformation clientInfo={clientContract.data} classes={classes} />}
      >
        <FormGroup className={classes.form_right}>
          <ActionTabs clientInfo={clientContract.data} />
          <div className={classes.form_divider} />
        </FormGroup>
      </FormDrawer>
    </React.Fragment>
  )
}
