import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'

import { makeStyles } from '@material-ui/core/styles'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CloseIcon from '@material-ui/icons/Close'
import Slide from '@material-ui/core/Slide'

// Components
import PdfViewer from './PdfViewer'
import downloadServices from './downloadServices'
import getPreviewReport from './getPreviewReport'

const useStyles = makeStyles(theme => ({
  appBar: {
    position: 'relative'
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1
  }
}))

const Transition = React.forwardRef(function Transition (props, ref) {
  return <Slide direction='up' ref={ref} {...props} />
})

export default function Fullscreen (props) {
  const { close, open, soaNumber, options, type } = props
  console.group('soa number and options')
  console.log(soaNumber)
  console.log(options)
  console.groupEnd()
  const classes = useStyles()
  const [file, setFile] = React.useState(null)
  React.useEffect(() => {
    getPreviewReport({ options: options, soaNumber: soaNumber, setFile: setFile, type: type })
  }, [options, soaNumber])
  return (
    <div>
      <Dialog fullScreen open={open} onClose={close} TransitionComponent={Transition}>
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton edge='start' color='inherit' onClick={close} aria-label='close'>
              <CloseIcon />
            </IconButton>
            <Typography variant='h6' className={classes.title}>
              Preview Sheet
            </Typography>
          </Toolbar>
        </AppBar>
        <DialogContent style={{ padding: '0px' }}>
          {file ? (
            <PdfViewer
              soaNumber={soaNumber}
              options={options}
              file={file}
              download={() => downloadServices({ type: type, soaNumber: soaNumber, options: options })}
              type={type}
            />
          ) : (
            ''
          )}
        </DialogContent>
      </Dialog>
    </div>
  )
}
