import React from 'react'

import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'

import classes from './CustomSearchTextComponent.module.scss'

export default function CustomSearchTextComponent (props) {
  const { id, onChange, placeholder = '', width } = props

  return (
    <Paper style={{ width: width }} className={classes.root}>
      <InputBase
        id={id}
        className={classes.input}
        placeholder={placeholder}
        inputProps={{ 'aria-label': 'search google maps' }}
        onChange={e => onChange && onChange(e)}
        autoComplete='no'
      />
    </Paper>
  )
}
