import React from 'react'

import Table from '@material-ui/core/Table'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TablePagination from '@material-ui/core/TablePagination'
import TableRow from '@material-ui/core/TableRow'
import Paper from '@material-ui/core/Paper'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSort } from '@fortawesome/free-solid-svg-icons'

import { Link } from 'react-router-dom'

import CustomSearchTextComponent from '../../Components/CustomSearchTextComponent/CustomSearchTextComponent'

import classes from './TempTableForMemo.module.scss'
import { TableBody, Typography } from '@material-ui/core'

const headers = [
  {
    id: '1',
    numeric: false,
    disablePadding: false,
    label: 'Choose Transaction Type',
    width: 120,
    labelwidth: '120px'
  },
  {
    id: '2',
    numeric: false,
    disablePadding: false,
    label: 'Enter Member Account ID',
    width: 158,
    labelwidth: '100px'
  },
  {
    id: '1',
    numeric: false,
    disablePadding: false,
    label: 'Enter Description',
    width: 120,
    labelwidth: '120px'
  },
  {
    id: '1',
    numeric: false,
    disablePadding: false,
    label: 'Choose Tax Type',
    width: 90,
    labelwidth: '70px'
  },
  {
    id: '1',
    numeric: false,
    disablePadding: false,
    label: 'Enter Amount',
    width: 94,
    labelwidth: '90px'
  },
  {
    id: '1',
    numeric: false,
    disablePadding: false,
    label: 'Add',
    width: 10,
    labelwidth: '30px'
  }
]

export default function TempTableForMemo (props) {
  const { search, tableHeaders, tableHeight, children, pagination, size, sort } = props

  // Get width of the element
  // const [elemWidth, setElemWidth] = React.useState('')
  // const refElem = React.useRef(0)

  return (
    <div className={classes.root}>
      <Paper style={{ height: tableHeight }} className={classes.paper}>
        <div className={classes['table-wrapper']}>
          <Table className={classes.table} aria-labelledby='tableTitle' size={size}>
            <TableHead>
              <TableRow>
                {headers.map(header => (
                  <TableCell
                    // For getting width of the element
                    // ref={refElem => {
                    //   if (refElem) {
                    //     // setElemWidth(refElem.offsetWidth)
                    //     console.log(refElem.offsetWidth)
                    //   }
                    // }}
                    id={header.id}
                    key={header.id}
                    align={header.center ? 'center' : header.numeric ? 'right' : 'left'}
                    padding={header.disablePadding ? 'none' : 'default'}
                    sortDirection={false}
                    width={header.width}
                  >
                    {header.label !== 'Add' ? (
                      <div style={{ width: header.labelwidth }}>{header.label}</div>
                    ) : (
                      <Link to={`/`} style={{ color: '#0d5e40' }}>
                        + Add
                      </Link>
                    )}
                    {header.label !== '' && sort ? (
                      <FontAwesomeIcon className={classes['sort-icon']} style={{ float: 'right' }} icon={faSort} />
                    ) : (
                      ''
                    )}
                    {header.label !== '' && search && header.label !== 'Add' ? (
                      <CustomSearchTextComponent id={`${header.label}-search`} width={header.width} />
                    ) : (
                      ''
                    )}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            {/* {children} */}
            <TableBody>
              {/* <TableRow> */}
              <Typography align='center'>No transactions added yet</Typography>
              {/* </TableRow> */}
            </TableBody>
          </Table>
        </div>
        {pagination && <TablePagination rowsPerPageOptions={[5, 10, 25]} component='div' {...pagination} />}
      </Paper>
    </div>
  )
}
