// import numeral from 'numeral'
// import moment from 'moment'

const EllipsisFormatter = (value, tolerance) => {
  // value = value.substring(0, value.length - 1)
  value.toString().length > tolerance
    ? (value = value
      .toString()
      .slice(0, tolerance)
      .concat('...'))
    : (value = value
      .toString()
      .slice(0, tolerance)
      .concat(' '))

  return value
}

// TODO: Move to TextFormatter

export { EllipsisFormatter }
