import React from 'react'
import { Link, useHistory } from 'react-router-dom'

import moment from 'moment'

import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'

import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CloseIcon from '@material-ui/icons/Close'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'

import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'

import Grid from '@material-ui/core/Grid'

import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'
import SearchTextComponent from 'SharedComponents/SearchTextComponent/SearchTextComponent'
import CustomEnhancedTable from '../CustomEnhancedTable/CustomEnhancedTable'
import { GrabByString } from 'Utilities/GrabObject'

import classes from './ViewAll.module.scss'

export default function CustomizedDialogs (props) {
  const {
    config,
    tile,
    title,
    contents,
    route,
    linkCaption,
    viewAll,
    setViewAll,
    passProperty,
    subProperty,
    subSubProperty,
    changeViewList,
    count,
    modal,
    preSoa,
    preSoaAnalyst
  } = props

  const history = useHistory()

  const tableHeaders = config[tile].headers
  const tableColumnSequence = config[tile].sequence
  const pagination = { count: count, rowsPerPage: 10, page: Math.ceil(count / 10) }
  const handleOnClose = () => {
    setViewAll(false)
    history.push('/')
  }

  const changeView = params => {
    changeViewList(params)
    pagination.page = 6
    console.log(pagination)
  }

  return (
    <Dialog
      fullWidth
      maxWidth='lg'
      onClose={handleOnClose}
      aria-labelledby='customized-dialog-title'
      open={viewAll}
      className={classes.root}
      PaperProps={{ style: { height: '100%', display: 'flex', backgroundColor: '#f4f6f8' } }}
    >
      <DialogTitle disableTypography className={classes['header-container']}>
        <div className={classes.header}>
          <Typography id='modal-title' variant='h6' className={classes.title}>
            {title}
          </Typography>
          <IconButton
            id='modal-close-button'
            aria-label='close'
            className={classes['close-button']}
            onClick={handleOnClose}
          >
            <CloseIcon />
          </IconButton>
          <div className={classes.clear} />
        </div>
      </DialogTitle>
      <DialogContent className={classes['content-container']}>
        <div className={classes['search-container']}>
          <div id='modal-search-field' className={classes['search-field']}>
            <SearchTextComponent />
          </div>
          <Link id='display-settings-link' className={classes['display-settings']} to='/' color='primary' size='medium'>
            <IconButton id='display-settings-icon'>
              <ViewListRoundedIcon className={classes['display-settings-icon']} />
              <Typography className='text-link underline'>Display Settings</Typography>
            </IconButton>
          </Link>
        </div>
        {/* For Pre Soa to Analyze Modal */}
        {preSoa ? (
          <div className={classes.count}>
            <Grid container spacing={2} direction='row'>
              <Grid item xs={12}>
                <Grid container justify='center' spacing={10}>
                  <Grid item className={classes.grids}>
                    <Typography variant='h4' align='center' className={classes.name}>
                      0
                    </Typography>
                    <Typography variant='h6'>TO ANALYZE</Typography>
                  </Grid>
                  <Grid item className={classes.grids}>
                    <Typography variant='h4' align='center' className={classes.name}>
                      0
                    </Typography>
                    <Typography variant='h6' align='center'>
                      APPROVED
                    </Typography>
                  </Grid>
                  <Grid item className={classes.grids}>
                    <Typography variant='h4' align='center' className={classes.name}>
                      0
                    </Typography>
                    <Typography variant='h6' align='center'>
                      DISAPPROVED
                    </Typography>
                    <Typography className={classes.smtext} align='center'>
                      (FOR REVISION)
                    </Typography>
                  </Grid>
                  <Grid item className={classes.grids}>
                    <Typography variant='h4' align='center' className={classes.name}>
                      0
                    </Typography>
                    <Typography variant='h6' align='center'>
                      VOID
                    </Typography>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </div>
        ) : (
          ''
        )}
        {/* end of for pre soa to Analyze Modal */}
        <CustomEnhancedTable
          count={count}
          changeView={changeView}
          tableHeaders={tableHeaders}
          pagination={pagination}
          search
          sort
        >
          <TableBody>
            {contents.map((row, contentsIndex) => (
              <TableRow hover tabIndex={-1} key={contentsIndex}>
                {tableColumnSequence.map((columnName, index) => (
                  <TableCell
                    id={`${columnName}-${contentsIndex}`}
                    key={index}
                    align={
                      new RegExp(['amount', 'sub_total'].join('|')).test(columnName.toString().toLowerCase())
                        ? 'right'
                        : 'left'
                    }
                  >
                    {columnName.includes('.') &&
                    new RegExp(['amount', 'sub_total'].join('|')).test(columnName.toString().toLowerCase())
                      ? CurrencyFormatter(GrabByString(row, columnName))
                      : columnName.includes('.')
                        ? moment(GrabByString(row, columnName), moment.ISO_8601).isValid()
                          ? DateFormatterByAbbreviation(row[columnName])
                          : GrabByString(row, columnName)
                        : row[columnName]}
                  </TableCell>
                ))}
                {linkCaption ? (
                  <TableCell id={`${linkCaption.toLowerCase()}-cell-${contentsIndex}-modal`} align='center'>
                    <Link
                      id={`${linkCaption.toLowerCase()}-link-${contentsIndex}-modal`}
                      to={
                        passProperty
                          ? passProperty.includes('.') || subProperty
                            ? subProperty
                              ? subSubProperty
                                ? route + row[passProperty] + '/' + row[subProperty][subSubProperty] + modal
                                : route + GrabByString(row, passProperty) + '/' + GrabByString(row, subProperty)
                              : route + GrabByString(row, passProperty)
                            : route + GrabByString(row, passProperty) + (preSoaAnalyst === true ? '/analyst' : '')
                          : route
                      }
                    >
                      <Typography
                        id={`${linkCaption.toLowerCase()}-text-${contentsIndex}-modal`}
                        variant='caption'
                        className='text-link underline'
                      >
                        {linkCaption}
                      </Typography>
                    </Link>
                  </TableCell>
                ) : (
                  ''
                )}
              </TableRow>
            ))}
          </TableBody>
        </CustomEnhancedTable>
      </DialogContent>
    </Dialog>
  )
}
