import React from 'react'
// Components
import PdfViewer from 'StandardComponents/PdfViewer/PdfViewer'

// Service
import BillingSOAReportService from '../../Services/Api/BillingSOAReportService'

import classes from './Corporate.module.scss'

export default function Corporate (props) {
  const { soaNumber } = props
  const [file, setFile] = React.useState(null)
  const type = 'category'
  React.useEffect(() => {
    BillingSOAReportService.getReport({ type: type, soaNumber: soaNumber }).then(response => {
      const blob = new Blob([response.data])
      setFile(blob)
    })
  }, [type])
  const downloadServices = args => {
    const { type } = args
    BillingSOAReportService.getReport({ type: type, soaNumber: soaNumber }).then(response => {
      const url = URL.createObjectURL(new Blob([response.data]))
      const link = document.createElement('a')
      link.href = url
      link.setAttribute('download', 'soa-corporate.pdf')
      document.body.appendChild(link)
      link.click()
    })
  }
  return (
    <div className={classes.root}>
      {file ? (
        <PdfViewer
          soaNumber={soaNumber}
          options={'corporate'}
          file={file}
          download={() => downloadServices({ type: 'category' })}
          type={'category'}
        />
      ) : (
        ''
      )}
    </div>
  )
}
