import React from 'react'
import { Switch, Route } from 'react-router-dom'

// Routes by role

import BillingModuleRoutes from './Routes'
// import ClaimsDetailsEncoderRoutes from './ClaimsDetailsEncoder/Routes'
// import ClaimsAnalystRoutes from './ClaimsAnalyst/Routes'
// import PaymentRequestRoutes from './PaymentRequest/Routes'
// import BatchEncodingServices from 'Services/Api/BatchEncodingServices'

export default function BillingModule ({ history }) {
  const [userRole, setUserRole] = React.useState('')
  const [isLoading, setIsLoading] = React.useState(true)

  React.useEffect(() => {
    document.title = 'HIMS - Billing'
    const { role } = JSON.parse(localStorage.getItem('me'))
    setUserRole(role)
    setIsLoading(false)
  }, [])

  const billingModuleLogic = () => {
    return <Route path='/' component={BillingModuleRoutes} />
  }

  return (
    <React.Fragment>
      {isLoading ? (
        ''
      ) : (
        <Switch>
          {userRole === 'BILLING_ANALYST' && billingModuleLogic()}
          {userRole === 'BILLING_SUPERVISOR' && billingModuleLogic()}
          {/* {userRole === 'BILLING_SUPERVISOR' && billingSupervisorLogic()} */}
          {/* {userRole === 'CLAIMS_DETAILS_ENCODER' && <Route path='/' component={ClaimsDetailsEncoderRoutes} />} */}
          {/* {userRole === 'CLAIMS_ANALYST' && <Route path='/' component={ClaimsAnalystRoutes} />} */}
          {/* {userRole === 'PR_OFFICER' && <Route path='/' component={PaymentRequestRoutes} />} */}
        </Switch>
      )}
    </React.Fragment>
  )
}
