import React from 'react'
import Paper from '@material-ui/core/Paper'

import { Typography } from '@material-ui/core'

import { CurrencyFormatter } from 'Utilities/TextFormatter'

import classes from './ContentHeader.module.scss'

export default function ContentHeader (props) {
  const { children, summaryFields, summaryValues, fieldsWidth } = props
  const highestValue = [
    (summaryValues[0] + '').length,
    (summaryValues[1] + '').length,
    (summaryValues[2] + '').length
  ].sort(function (a, b) {
    return b - a
  })
  const valuesWidth = () => {
    switch (highestValue[0]) {
      case 5:
        return '140px'
      case 6:
        return '150px'
      case 7:
        return '160px'
      case 8:
        return '170px'
      case 9:
        return '180px'
      case 10:
        return '190px'
      case 11:
        return '200px'
      default:
        return '130px'
    }
  }

  return (
    <div className={classes.root}>
      <Paper className={classes.container}>
        <div className={classes['content-header-details']}>{children}</div>
        <div className={classes['content-header-summary']}>
          <div className={classes['summary-wrapper']}>
            <div className={classes['summary-header']}>
              <Typography id='summary-title' align='center'>
                SUMMARY
              </Typography>
            </div>
            <div className={classes['summary-content']}>
              <div className={classes.top}>
                <div style={{ width: fieldsWidth }} className={classes.fields}>
                  <Typography id='summary-field-0' variant='caption'>
                    {summaryFields[0]}
                  </Typography>
                  <br />
                  <Typography id='summary-field-1' variant='caption'>
                    {summaryFields[1]}
                  </Typography>
                </div>
                <div style={{ width: valuesWidth() }} className={classes.values}>
                  <Typography id='summary-value-full-0' variant='caption'>
                    <span className={classes['php-text']}>PHP</span>
                    <span id='summary-value--0' className={classes.value}>
                      {CurrencyFormatter(summaryValues[0])}
                    </span>
                  </Typography>
                  <br />
                  <Typography id='summary-value-full-1' variant='caption'>
                    <span className={classes['php-text']}>PHP</span>
                    <span id='summary-value-1' className={classes.value}>
                      {CurrencyFormatter(summaryValues[1])}
                    </span>
                  </Typography>
                </div>
              </div>
              <hr style={{ width: '98%', float: 'right' }} />
              <div className={classes.bottom}>
                <div style={{ width: fieldsWidth }} className={classes.fields}>
                  <Typography id='summary-field-2'>{summaryFields[2]}</Typography>
                </div>
                <div style={{ width: valuesWidth() }} className={classes.values}>
                  <Typography id='summary-value-2'>PHP {CurrencyFormatter(summaryValues[2])}</Typography>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Paper>
    </div>
  )
}
